import { cameraDirection } from "../constVariables";

// 
export const cameraDirectionFormate = (direction: string | number) => {
  const filters = {};
  cameraDirection.forEach(item => {
    filters[item.value] = item.label;
  });
  return filters[direction] || direction;
};
