
import {
  defineComponent,
  reactive,
  ref,
  toRefs,
  onMounted,
  watch,
  nextTick
} from "vue";
import api from "@/api/alarm";
import { formatTime } from "@/utils";
import { ElMessage } from "element-plus";
// import { ElMessage } from "element-plus";

export default defineComponent({
  name: "Handle",
  props: {
    alarmType: {
      required: false,
      default: ""
    }
  },
  setup(props) {
    const params = reactive({
      status: ["", "", "", "active"],
      showAlarms: [] as any,
      alarms: [],
      styleArr: [
        { background: "#00A67B" },
        { background: "#FF7352" },
        { background: "#D9354E" }
      ],
      colorStyleArr: [
        { color: "#00A67B" },
        { color: "#FF7352" },
        { color: "#D9354E" }
      ],
      flag: "",
      size: 10,
      page: 1,
      loading: false,
      oldY: undefined,
      disposeStatus: "", // 
      typeid: "", // id
      alarmLevel: "" as string | number,
      alarmTypeArr: [], // 
      alarmTypeName: "",
      total: 0,
      contentVisiable: false // 
    });

    const itemsRef = ref();
    const alarmBarRef = ref();
    const dataRef = toRefs(params);
    async function getAlarmList() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.size,
        ShowSceneNum: "",
        SourceType: props.alarmType,
        alarmLevel: params.alarmLevel,
        sTime: "",
        eTime: "",
        alarmProgress: params.disposeStatus
      };
      const res = await api.getAllAlamList(param);
      const { result, data } = res as any;

      if (result.success) {
        params.showAlarms = [...params.showAlarms, ...data.content];
        params.total = data.count;
      }
      params.loading = false;
    }

    // 
    function alarmLevel(n: number) {
      const arr = ["", "", ""];
      return arr[n];
    }

    function scrollAlarms(e: any) {
      const newY = e.targetTouches[0].pageX;
      if (params.oldY === undefined) {
        params.oldY = newY;
      }
      const y = params.oldY === undefined ? newY : params.oldY;
      alarmBarRef.value.scrollTop -= newY - y;
      params.oldY = newY;
    }
    // 
    function handleStatus(count: string) {
      params.loading = true;
      if (params.disposeStatus == count) {
        params.loading = false;
        return;
      }
      params.page = 1;
      params.flag = count;
      params.disposeStatus = count;
      params.showAlarms = [];
      getAlarmList();
    }
    // 
    function lazyLoad(e: any) {
      const itemLength = itemsRef.value && itemsRef.value.offsetHeight;
      const ulLength = alarmBarRef.value.offsetHeight;
      if (
        e.target.scrollTop >
        itemLength * params.page * params.size - ulLength
      ) {
        if (params.showAlarms.length < params.page * params.size) {
          return;
        }

        params.page += 1;
        params.loading = true;
        getAlarmList();
      }
    }
    // 
    function handleLevel(count: number) {
      params.loading = true;
      if (
        params.alarmLevel === count ||
        (params.alarmLevel === "" && count === 3)
      ) {
        params.loading = false;
        return;
      }
      params.status = ["", "", "", ""];
      params.status[count] = "active";
      if (count === 3) {
        params.alarmLevel = "";
      } else {
        params.alarmLevel = count;
      }
      params.page = 1;
      params.showAlarms = [];
      getAlarmList();
    }

    // 
    function alarmDetail(id: string | number) {
      console.log(id);
      params.contentVisiable = true;
    }

    // 
    function handleAlarm(id: string | number, index: number) {
      // const res = api.handleAlarmData;
      // const { data } = res as any;
      // getAlarmList();
      params.showAlarms.splice(index, 1);
      params.total = params.total - 1;
      ElMessage.success("!");
    }

    //
    function handleAll() {
      params.showAlarms = [];
      // const res = api.dealAlarms;
      // const { data } = res as any;
      // getAlarmList();
      ElMessage.success("!");
    }

    onMounted(() => {
      getAlarmList();
    });

    watch(
      () => props.alarmType,
      val => {
        nextTick(() => {
          params.page = 1;
          params.showAlarms = [];
          getAlarmList();
        });
      }
    );
    return {
      ...dataRef,
      itemsRef,
      alarmBarRef,
      formatTime,
      alarmLevel,
      scrollAlarms,
      handleStatus,
      lazyLoad,
      handleLevel,
      alarmDetail,
      handleAll,
      handleAlarm
    };
  }
});
