<!--
 * @Description: 
 * @Author: huxianc
 * @Date: 2020-12-24 14:56:24
 * @LastEditors: huxianc
 * @LastEditTime: 2021-01-28 18:01:14
-->
<template>
  <main class="home-wrapper">
    <div id="container"></div>

    <!--  start -->
    <el-collapse-transition>
      <main-nav
        v-show="visibleObj.nav && !status.isDrawFence"
        ref="mainNavRef"
        @navClick="navClick"
      />
    </el-collapse-transition>
    <!--  end -->

    <!--  start -->
    <el-drawer
      v-model="drawerInfo.showList"
      direction="rtl"
      size="50%"
      :with-header="false"
      @close="resetNavIndex"
      append-to-body
    >
      <div class="drawer-container">
        <!-- drawer - title -->
        <div class="drawer-header">
          <div class="drawer-title">{{ drawerInfo.drawerTitle }}</div>
          <i class="el-icon-close" @click="drawerInfo.showList = false" />
        </div>
        <!-- drawer content -->
        <el-tabs
          v-model="drawerInfo.activeName"
          type="card"
          @tab-click="equipSwitchClick"
        >
          <el-tab-pane label="" :name="types.camera">
            <!-- v-if tabsapi -->
            <camera-list
              v-if="drawerInfo.activeName === types.camera"
              :form-data="detailsForm.formData"
              @showEquipDetails="showEquipDetails"
              @addEquip="addEquip"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.basestation">
            <basestation-list
              v-if="drawerInfo.activeName === types.basestation"
              :form-data="detailsForm.formData"
              @showEquipDetails="showEquipDetails"
              @addEquip="addEquip"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.labelList">
            <label-list
              v-if="drawerInfo.activeName === types.labelList"
              :form-data="detailsForm.formData"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
          <el-tab-pane label="" :name="types.person">
            <person-list
              v-if="drawerInfo.activeName === types.person"
              :form-data="detailsForm.formData"
              @showEquipDetails="showEquipDetails"
            />
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-drawer>
    <!--  end -->

    <!-- table dialog start -->
    <el-dialog
      title=""
      v-model="fenceListInfo.tableVisible"
      width="50%"
    >
      <el-form :model="fenceListInfo.params" label-width="80px" :inline="true">
        <el-form-item label>
          <el-button type="primary" @click="handleAddFence"></el-button>
        </el-form-item>
        <el-form-item label>
          <el-button
            type="primary"
            @click="toggleAllFenceVisible(true)"
            v-show="fenceListInfo.tableData.length > 0"
            ></el-button
          >
        </el-form-item>
        <el-form-item label>
          <el-button
            type="primary"
            @click="toggleAllFenceVisible(false)"
            v-show="fenceListInfo.tableData.length > 0"
            ></el-button
          >
        </el-form-item>
      </el-form>
      <el-table
        v-loading="fenceListInfo.loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        :data="fenceListInfo.tableData"
        border
        tooltip-effect="dark"
      >
        <el-table-column
          prop="areaname"
          label=""
          width
          align="center"
          show-overflow-tooltip
        />
        <el-table-column label="" width align="center">
          <template #default="{ row }">
            <el-switch
              v-model="row.show"
              active-color="#13ce66"
              @change="changeAreaVisible($event, row.areaids)"
            />
          </template>
        </el-table-column>
        <el-table-column label="" width="250" align="center">
          <template #default="{ row }">
            <el-button type="primary" size="mini" @click="handleEditFence(row)"
              ></el-button
            >
            <el-button type="primary" size="mini" @click="handleSeeRules(row)"
              ></el-button
            >
            <el-popconfirm
              :title="` ${row.areaname} `"
              @confirm="handleDelFence(row)"
            >
              <template #reference>
                <el-button type="danger" size="mini" style="margin-left: 10px"
                  ></el-button
                >
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="
          fenceListInfo.total &&
            fenceListInfo.tableData &&
            fenceListInfo.tableData.length
        "
        v-model:current-page="fenceListInfo.params.curpage"
        :page-size="10"
        layout="total, prev, pager, next, jumper"
        :total="fenceListInfo.total"
        background
        @current-change="fenceCurrentChange"
      />
    </el-dialog>
    <!-- table dialog end -->

    <!--   dialog start -->
    <el-dialog
      :title="`${fenceInfo.dialogType === 0 ? '' : ''}`"
      v-model="fenceInfo.visible"
      center
      width="800px"
    >
      <el-form
        ref="fenceInfoFormRef"
        :model="fenceInfo.form"
        :rules="fenceInfo.rules"
        label-width="120px"
      >
        <el-row :gutter="0">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="name">
              <el-input
                v-model.trim="fenceInfo.form.name"
                clearable
                style="width: 200px"
                placeholder=""
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="">
          <el-transfer
            v-model="fenceInfo.form.chosenCamera"
            filterable
            filter-placeholder=""
            :data="fenceInfo.allMapCamera"
            :titles="['', '']"
            :props="transferProps"
          >
            <template #default="{ option }">
              <span>{{ option.dname }}</span>
            </template>
          </el-transfer>
        </el-form-item>

        <el-form-item label="" required>
          <el-button type="warning" size="mini" @click="handleFenceDraw">
            {{
              `${drawFenceInfo.clonePoints.length === 0 ? "" : ""}`
            }}
          </el-button>
        </el-form-item>
      </el-form>

      <template #footer>
        <span>
          <el-button @click="fenceInfo.visible = false"></el-button>
          <!-- <el-button type="primary" @click="handleSubmitFence"></el-button> -->
          <el-button
            type="primary"
            :disabled="!drawFenceInfo.clonePoints.length"
            @click="handleSubmitFence"
            ></el-button
          >
        </span>
      </template>
    </el-dialog>
    <!--   dialog end -->

    <!--  buttons start -->
    <el-collapse-transition>
      <fence-box
        v-if="status.isDrawFence"
        @revoke="handleRevokeFence"
        @redraw="handleRedrawFence"
        @cancel="handleCancelDrawFence"
        @finish="handleFinishDrawFence"
      />
    </el-collapse-transition>
    <!--  buttons end -->

    <!--  table start -->
    <el-dialog title="" v-model="ruleListInfo.visiable" width="80%">
      <el-form :model="fenceListInfo.params" label-width="80px" :inline="true">
        <el-form-item label>
          <el-button type="primary" @click="handleAddRule"></el-button>
        </el-form-item>
      </el-form>
      <el-table
        v-loading="ruleListInfo.loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        :data="ruleListInfo.tableData"
        border
        tooltip-effect="dark"
      >
        <el-table-column
          label=""
          prop="labelType"
          width
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          label=""
          width
          align="center"
          show-overflow-tooltip
        >
          <template #default="{ row }">
            {{ row.isloop ? "" : "" }}
          </template>
        </el-table-column>
        <el-table-column label="" width align="center" prop="stime">
        </el-table-column>
        <el-table-column label="" width align="center" prop="etime">
        </el-table-column>

        <el-table-column label="" width="210" align="center">
          <template #default="{ row }">
            <el-button type="primary" size="mini" @click="handleEditRule(row)"
              ></el-button
            >

            <el-popconfirm
              :title="``"
              @confirm="handleDelRule(row)"
            >
              <template #reference>
                <el-button type="danger" size="mini" style="margin-left: 10px"
                  ></el-button
                >
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="
          ruleListInfo.total &&
            ruleListInfo.tableData &&
            ruleListInfo.tableData.length
        "
        v-model:current-page="fenceListInfo.params.curpage"
        layout="total, prev, pager, next, jumper"
        :page-size="10"
        :total="ruleListInfo.total"
        background
        @current-change="ruleCurrentChange"
      />
    </el-dialog>
    <!--  table end -->

    <!--  start -->
    <el-dialog
      :title="(ruleItemDetail.dialogType === 0 ? '' : '') + ''"
      v-model="ruleItemDetail.visiable"
      width="60%"
      center
    >
      <el-form
        ref="ruleFormRef"
        :model="ruleItemDetail.form"
        :rules="ruleItemDetailRule"
        label-width="100px"
      >
        <el-row :gutter="0">
          <el-col :span="12" :offset="0">
            <el-form-item label="">
              <el-switch
                v-model="ruleItemDetail.form.isLoop"
                active-color="#13ce66"
                inactive-color="#ff4949"
              >
              </el-switch>
            </el-form-item>
          </el-col>

          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="permitType">
              <el-select
                v-model="ruleItemDetail.form.permitType"
                placeholder=""
                clearable
              >
                <el-option
                  v-for="item in areaRulesOption"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="limitedTime">
              <el-input
                v-model.trim="ruleItemDetail.form.limitedTime"
                placeholder="0"
                clearable
                style="max-width: 200px"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- IsLoop=true,false -->

        <el-row v-if="ruleItemDetail.form.isLoop" :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="startTime">
              <el-time-picker
                v-model="ruleItemDetail.form.startTime"
                placeholder=""
                @change="timerPickerChange"
              >
              </el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="endTime">
              <el-time-picker
                v-model="ruleItemDetail.form.endTime"
                placeholder=""
              >
              </el-time-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row v-else :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="startTime">
              <el-date-picker
                v-model="ruleItemDetail.form.startTime"
                type="datetime"
                placeholder=""
                @change="ruleItemDetail.form.endTime = ''"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="endTime">
              <el-date-picker
                v-model="ruleItemDetail.form.endTime"
                type="datetime"
                placeholder=""
                :disabled="!ruleItemDetail.form.startTime"
                :disabledDate="ruleDisabledDate"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="24" :offset="0">
            <el-form-item label="">
              <el-input v-model="ruleItemDetail.form.memo" type="textarea" />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <template #footer>
        <span>
          <el-button @click="ruleItemDetail.visiable = false"></el-button>
          <el-button type="primary" @click="handleSubmitRule"></el-button>
        </span>
      </template>
    </el-dialog>
    <!--  end -->

    <!-- table dialog start -->
    <el-dialog
      title=""
      v-model="alarmListInfo.tableVisible"
      width="60%"
    >
      <el-form :model="alarmListInfo.params" label-width="80px" :inline="true">
        <el-form-item label>
          <el-select
            v-model="alarmListInfo.SourceType"
            placeholder=""
            clearable
            filterable
            @change="
              () => {
                getAlarmListByCondition();
              }
            "
          >
            <el-option
              v-for="item in alarmType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label>
          <el-date-picker
            v-model="alarmListInfo.sTime"
            type="datetime"
            placeholder=""
            @change="getAlarmListByCondition"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label>
          <el-date-picker
            v-model="alarmListInfo.eTime"
            type="datetime"
            placeholder=""
            @change="getAlarmListByCondition"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label>
          <el-select
            v-model="alarmListInfo.alarmLevel"
            placeholder=""
            style="width:120px"
            clearable
            filterable
            @change="
              () => {
                getAlarmListByCondition();
              }
            "
          >
            <el-option label="" value="0"> </el-option>
            <el-option label="" value="1"> </el-option>
            <el-option label="" value="2"> </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-table
        v-loading="alarmListInfo.loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        :data="alarmListInfo.tableData"
        border
        tooltip-effect="dark"
      >
        <el-table-column
          label=""
          width="100"
          align="center"
          show-overflow-tooltip
        >
          <template #default="{ row }">
            {{
              row.alarmLevel === 0
                ? ""
                : row.alarmLevel === 1
                ? ""
                : ""
            }}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="SourceType"
          width="100"
          align="center"
          show-overflow-tooltip
        />
        <el-table-column
          prop="alarmContent"
          label=""
          width
          show-overflow-tooltip
        />
        <el-table-column label="" width="180" show-overflow-tooltip>
          <template #default="{ row }">
            {{ formatTime(row.CreateTime) }}
          </template>
        </el-table-column>
        <el-table-column label="" width="200" align="center">
          <template #default="{ row }">
            <el-button
              type="primary"
              size="mini"
              @click="handleDetailAlarm(row)"
              ></el-button
            >
            <el-button type="primary" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-show="
          alarmListInfo.total &&
            alarmListInfo.tableData &&
            alarmListInfo.tableData.length
        "
        v-model:current-page="alarmListInfo.params.curpage"
        :page-size="alarmListInfo.params.size"
        layout="total, prev, pager, next, jumper"
        :total="alarmListInfo.total"
        background
        @current-change="alarmCurrentChange"
      />
    </el-dialog>
    <!-- table dialog end -->

    <!--  start -->
    <el-dialog title="" v-model="alarmInfo.visible" width="60%" center>
      <el-form ref="ruleFormRef" :model="alarmInfo.form" label-width="100px">
        <el-row :gutter="0">
          <el-col :span="12" :offset="0">
            <el-form-item label="" prop="SourceName"> </el-form-item>
          </el-col>

          <el-col :span="12" :offset="0">
            <el-form-item label=""> </el-form-item>
          </el-col>

          <el-col :span="12" :offset="0">
            <el-form-item label=""> </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12" :offset="0">
            <el-form-item label=""> </el-form-item>
          </el-col>
          <el-col :span="12" :offset="0">
            <el-form-item label=""> </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="24" :offset="0">
            <el-form-item label=""> </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <template #footer>
        <span> </span>
      </template>
    </el-dialog>
    <!--  end -->

    <!--  start -->
    <contextmenu-content
      v-if="visibleObj.contextmenu"
      :type="contextmenuTypeRef"
      :contxtmenu-position="contxtmenuPosition"
      :contextmenu-list="contextmenuList"
      @clicked="visibleObj.contextmenu = false"
      @seeNowVideo="seeNowVideo"
      @seeHistoryVideo="seeHistoryVideo"
      @showDetails="showDetails"
      @trackPlayback="handleClickTrackPlayback"
      @edit="detailsEdit"
    />
    <!--  end -->

    <!--  form start -->
    <form-content
      v-show="visibleObj.detailsForm"
      ref="formContentComponentRef"
      :is-add="detailsForm.isAdd"
      :editable="detailsForm.editable"
      :form-data="detailsForm.formData"
      :form-type="contextmenuTypeRef"
      :map-id="mapIdRef"
      :x="detailsForm.x"
      :y="detailsForm.y"
      @close="hideDetails"
      @cancelForm="cancelForm"
      @changeDirection="changeDirection"
      @updateX="updateX"
      @updateY="updateY"
      @confirm="handleDetailConfirm"
    />
    <!--  form end -->
    <!-- lfq start -->
    <div
      v-drag-dialog="`monitor-header,custom-monitor`"
      v-show="visibleObj.monitorListFlag"
    >
      <div class="monitorlist-panel custom-monitor">
        <header class="monitor-header">
          <div class="title">{{ monitorListInfo.monitorTitle }}</div>
          <button class="closebtx" @click="handleCloseAllVideo">
            <i class="el-icon el-icon-close" />
          </button>
        </header>
        <div
          v-for="(value, index) in monitorListInfo.newMonitorList"
          v-show="value.active"
          :key="index"
          :class="monitorListInfo.monitorInner"
        >
          <monitor-list
            ref="monitorListPanelRef"
            :camera-data="value"
            @changeMonitorListPanelState="
              watchMonitorListPanelState(value.camera.id)
            "
            :cameraData="value"
            :livePlayCount="monitorListInfo.livePlayCount"
          />
        </div>
      </div>
    </div>
    <!--  end -->
    <!--lfq start-->
    <div
      v-drag-dialog="`monitor-header,custom-monitor`"
      v-show="visibleObj.historyMonitorListFlag"
    >
      <div class="monitorlist-panel custom-monitor">
        <header class="monitor-header">
          <div class="title">{{ monitorListInfo.monitorTitle }}</div>
          <button class="closebtx" @click="handleCloseAllHistoryVideo">
            <i class="el-icon el-icon-close" />
          </button>
        </header>
        <div
          v-for="(value, index) in monitorListInfo.newHistoryMonitorList"
          v-show="value.active"
          :key="index"
          :class="monitorListInfo.monitorHistoryInner"
        >
          <history-monitor
            ref="hitoryMonitorListPanelRef"
            :history-camera-data="value"
            @changeHistoryMonitorListPanelState="
              watchHistoryMonitorListPanelState(value.camera.id)
            "
            :historyCameraData="value"
            :historyPlayCount="monitorListInfo.historyPlayCount"
          />
        </div>
      </div>
    </div>
    <!--lfq end-->
    <!--  start -->
    <el-dialog
      title=""
      v-model="trackPlayback.show"
      width="400px"
      center
      @close="handleCloseContextmenu"
    >
      <el-form
        ref="trackPlaybackFormRef"
        :model="trackPlayback.form"
        :rules="trackPlayback.rules"
        label-width="100px"
        :inline="false"
        size="small"
      >
        <el-form-item label="" prop="date">
          <el-date-picker
            v-model="trackPlayback.form.date"
            type="date"
            placeholder=""
            :picker-options="trackPlayback.datePickerOptions"
          />
        </el-form-item>

        <el-form-item label="" prop="startTime">
          <el-time-select
            v-model="trackPlayback.form.startTime"
            placeholder=""
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }"
            @change="trackPlayback.form.endTime = ''"
          />
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-time-select
            v-model="trackPlayback.form.endTime"
            placeholder=""
            :disabled="!trackPlayback.form.startTime"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30',
              minTime: trackPlayback.form.startTime
            }"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span>
          <el-button @click="trackPlayback.show = false"></el-button>
          <!-- <el-button
            v-debounce-click="handleClickSeeTrackPlayback"
            type="primary"
            ></el-button
          > -->
          <el-button @click="handleClickSeeTrackPlayback" type="primary"
            ></el-button
          >
        </span>
      </template>
    </el-dialog>
    <!--  end -->

    <!--  start-->
    <div v-show="status.equipImgFollow" ref="equipImgRef" class="equip-img" />
    <!--  end-->

    <!--  start-->
    <div class="warning-container" :style="{ right: warningWidth }">
      <div class="warning-wrap">
        <div class="warning-show-button" @click="showWarningList"></div>
        <div class="warning-content">
          <el-form
            :model="fenceListInfo.params"
            label-width="80px"
            :inline="true"
          >
            <el-select
              v-model="alarmForm.type"
              placeholder=""
              clearable
              filterable
              style="width: 98%"
              @change="() => {}"
            >
              <el-option
                v-for="item in alarmType"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <br />
            <transition name="slide-fade">
              <handle :alarmType="alarmForm.type"></handle>
            </transition>
          </el-form>
        </div>
      </div>
    </div>
    <!--  end-->

    <!--  start -->
    <layer-person-list
      v-if="visibleObj.layerPerson"
      :person-list="layerPerson"
      :mouse-position="layerPosition"
    />
    <!--  end -->

    <!--  start -->
    <div
      class="control-menu"
      @click="visibleObj.controlPannel = !visibleObj.controlPannel"
    >
      <i class="el-icon-setting"></i>
    </div>
    <!--  end -->

    <!--  start -->
    <div class="control-panel" v-show="visibleObj.controlPannel">
      <div class="control-title o-h">{{ mapNameRef }}</div>
      <el-select
        ref="headerSearchSelectRef"
        v-model="leftPannelSearch"
