    const contextmenuTypeRef = shallowRef<string>(TYPES.basestation); //  TYPES
    const contxtmenuPosition = shallowReactive<IStyleLeftTop>({
      left: 0,
      top: 0,
    }); //  
    const currentContextmenuRef = ref<Nullable<Shape>>(null); // shapeshadow

    const contextmenuList = computed(() => ALL_MENU[contextmenuTypeRef.value]);

    const detailsForm = reactive<IDetailsForm>({
      show: false,
      isAdd: false, // 
      editable: false, // 
      formData: null, // form
      x: 0, // x
      y: 0,
    });

    const trackPlayback = reactive({
      show: false, // 
      // form
      form: {
        date: new Date() || "",
        startTime: "08:30" || "",
        endTime: "08:45" || "",
      },
      rules: {
        date: commonRuleItem(""),
        startTime: commonRuleItem(""),
        endTime: commonRuleItem(""),
      },
      datePickerOptions: {
        disabledDate(time: Date) {
          return time.getTime() > Date.now();
        },
      },
      tween: null,
      steps: [],
      lineNode: null,
      linePos: [], // 
    });

    const visibleObj = shallowReactive({
      nav: true, // 
      waterAlarmPanel: false, // 
      contextmenu: false, // 
      detailsForm: false, // form
      monitorListFlag: false, //  lfq
      historyMonitorListFlag: false, //  lfq
      area: true, // 
      layerPerson: false, // layer 
      map: false, // 
      controlPannel: true, // 
    });

    const monitorListInfo = reactive({
      //  lfq
      monitorTitle: "",
      monitorInner: "monitor-inner-pannel", // class
      monitorHistoryInner: "monitor-inner-pannel", // class
      newMonitorList: [] as any[], // lfq
      newHistoryMonitorList: [] as any[], //  lfq
      cameraCount: 4, //  lfq
      liveCameraCount: 1, //  lfq
      historyCameraCount: 1, //  lfq
      livePlayCount: 1, //  lfq
      historyPlayCount: 1, //  lfq
      monitorListFlag: false, // lfq
      historyMonitorListFlag: false, // lfq
    });

    const drawerInfo = reactive({
      showList: false, // 
      drawerTitle: NAV_INFO[TYPES.basestation].title,
      activeName: TYPES.basestation, // 
    });

    const fenceInfo = reactive<IFenceInfo>({
      visible: false,
      dialogType: 0, // 0   1 
      form: {
        name: "",
        chosenCamera: [],
      },
      allMapCamera: [], // 
      rules: {
        name: commonRuleItem(""),
      },
    });

    const fenceListInfo = reactive<IFenceListInfo>({
      tableVisible: false,
      params: {
        curpage: 1,
      },
      loading: false,
      allData: [],
      tableData: [],
      total: 0,
    });

    const drawFenceInfo = reactive<IDrawFenceInfo>({
      message: null, // 
      points: [], // 
      clonePoints: [],
    });

    //  table
    const ruleListInfo = reactive<IRuleListInfo>({
      visiable: false,
      loading: false,
      allData: [],
      tableData: [],
      total: 0,
      curpage: 1,
    });
    //  
    const ruleFormRef = ref();
    const ruleItemDetail = reactive<IRuleItemDetail>({
      visiable: false,
      dialogType: 0, // 0  1 
      form: {
        name: "",
        memo: "",
        isLoop: false,
        limitedTime: "",
        startTime: "",
        endTime: "",
        permitType: "",
      },
    });

    //  > 
    const checkEndTime = (rule: any, value: Date, callback: Function) => {
      // 
      if (!ruleItemDetail.form.isLoop) {
        callback();
      } else {
        const ymd = dayjs(new Date()).format("YYYY-MM-DD");
        // value 
        const hms = dayjs(value).format("HH:mm:ss");
        // 
        const start = dayjs(ruleItemDetail.form.startTime).format("HH:mm:ss");
        const startTime = new Date(`${ymd} ${start}`);
        const endTime = new Date(`${ymd} ${hms}`);
        if (startTime.getTime() > endTime.getTime()) {
          return callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    const ruleItemDetailRule: IRuleItemDetailRule = {
      name: commonRuleItem(""),
      limitedTime: customFuncRule(checkPositiveInteger),
      permitType: commonRuleItem(""),
      startTime: commonRuleItem(""),
      endTime: [commonRuleItem(""), customFuncRule(checkEndTime)],
    };

    // 
    const ruleDisabledDate = computed(() => {
      return function (time: Date) {
        if (ruleItemDetail.form.startTime) {
          return time.getTime() < (ruleItemDetail.form.startTime as Date).getTime();
        }
        return false;
      };
    });

     const alarmInfo = reactive<IAlarmInfo>({
      visible: false,
      dialogType: 0, // 0   1 
      form: {
        CreateTime: "",
        IDS: "",
        Id: 0,
        SceneName: "",
        ShowSceneNum: "",
        SourceDbOrDeviceIdS: "",
        SourceName: "",
        SourcePointNum: "",
        SourceType: "",
        alarmContent: "",
        alarmLevel: 0,
        alarmProgress: "",
        dealAccount: "",
        dealMemo: "",
        dealTime: "",
      }
    });

    const alarmListInfo = reactive<IAlarmListInfo>({
      tableVisible: false,
      params: {
        curpage: 1,
        size: 8,
      },
      loading: false,
      allData: [],
      tableData: [],
      total: 0,
      ShowSceneNum: "",
      SourceType: "",
      alarmLevel: "",
      sTime: "",
      eTime: "",
      alarmProgress: ""
    });

    // 
    function timerPickerChange() {
      if (ruleItemDetail.form.endTime) {
        ruleItemDetail.form.endTime = "";
      }
    }

    const mousePos = { x: 0, y: 0 };

    const stage = ref<Stage | null>(null);

    function batchDraw() {
      (stage.value as Stage).batchDraw();
    }

    const layer = new Konva.Layer();
    const bgLayer = new Konva.Layer();
    const operationLayer = new Konva.Layer();

    const allGroup = {
      cameraGroup: new Konva.Group({ name: "cameraGroup" }),
      basestationGroup: new Konva.Group({ name: "basestationGroup" }),
      personGroup: new Konva.Group({ name: "personGroup" }),
      fenceGroup: new Konva.Group({ name: "fenceGroup" }),
      locateBubbleGroup: new Konva.Group({ name: "locateBubbleGroup" }),
    };

    const activeTarget = reactive<IActiveTarget>({
      id: 0,
      attrs: { x: 0, y: 0 }, // 
    });

    const equipmentInfo = reactive<IEquipmentInfo>({
      basestationData: { x: 0, y: 0 }, // 
      cameraData: { x: 0, y: 0 }, // 
      checkedNode: null, // konva
    });

    watch(monitorListInfo.newMonitorList, (val) => {
      let count = 0;
      val.forEach((item: { active: any }) => {
        const active = item.active;
        if (active) {
          count++;
        }
      });
      if (count === 0) {
        visibleObj.monitorListFlag = false;
      }
      monitorListInfo.livePlayCount = count;
      if (count === 1) {
        monitorListInfo.monitorInner = "monitor-inner-one";
      } else {
        monitorListInfo.monitorInner = "monitor-inner-pannel";
      }

      if (val.length > monitorListInfo.cameraCount) {
        if (val.length > monitorListInfo.cameraCount * 1000) {
          val.splice(0, val.length - monitorListInfo.cameraCount);
        }
        val.forEach((item: { active: any }, index: number) => {
          if (index < val.length - monitorListInfo.cameraCount) {
            item.active = false;
          }
        });
      }
    });
    watch(monitorListInfo.newHistoryMonitorList, (val) => {
      let count = 0;
      val.forEach((value: { active: any }) => {
        const active = value.active;
        if (active) {
          count++;
        }
      });
      if (count === 0) {
        visibleObj.historyMonitorListFlag = false;
      }
      monitorListInfo.historyPlayCount = count;
      if (count === 1) {
        monitorListInfo.monitorHistoryInner = "monitor-inner-one";
      } else {
        monitorListInfo.monitorHistoryInner = "monitor-inner-pannel";
      }

      if (val.length > monitorListInfo.cameraCount) {
        if (val.length > monitorListInfo.cameraCount * 1000) {
          val.splice(0, val.length - monitorListInfo.cameraCount);
        }
        val.forEach((value: { active: any }, index: number) => {
          if (index < val.length - monitorListInfo.cameraCount) {
            value.active = false;
          }
        });
      }
    });

    let cacheDrawFence: Nullable<Shape> = null;
    /**
     * @description: 
     * @param {boolean} isFinished 
     * @return {*}
     */
    function handleStartDrawFence(isFinished = false) {
      cacheDrawFence && cacheDrawFence.destroy();

      const pointsArr = drawFenceInfo.points;
      if (pointsArr.length === 0) {
        batchDraw();
        return;
      }

      const fenceArea = new Konva.Shape({
        ...areaColorConfig,
        sceneFunc: function (ctx, shape) {
          ctx.beginPath();
          ctx.moveTo(pointsArr[0].x, pointsArr[0].y);
          for (let i = 1; i < pointsArr.length; i++) {
            const { x, y } = pointsArr[i];
            ctx.lineTo(x, y);
          }

          ctx.stroke();
          if (isFinished) {
            ctx.closePath();
          }

          ctx.fillStrokeShape(shape);
        },
      });
      cacheDrawFence = fenceArea;
      allGroup.fenceGroup.add(fenceArea);
      batchDraw();
    }

    // 
    function handleRedrawFence() {
      drawFenceInfo.points = [];
      handleStartDrawFence();
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function handleRevokeFence() {
      const { points } = drawFenceInfo;
      const len = points.length;
      if (len === 0) {
        ElMessage.info("");
      } else if (len === 1 || len === 2) {
        handleRedrawFence();
      } else {
        points.splice(len - 2, 2);
        handleStartDrawFence();
      }
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function handleCancelDrawFence() {
      handleRedrawFence();

      status.isDrawFence = false;
      (drawFenceInfo.message as IMessageClose).close();
      drawFenceInfo.points = [];
      drawFenceInfo.clonePoints = [];
      fenceInfo.visible = true;
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function handleFinishDrawFence() {
      const { points } = drawFenceInfo;
      const len = points.length;
      if (len < 4) {
        ElMessage.info("");
      } else {
        (drawFenceInfo.message as IMessageClose).close();

        status.isDrawFence = false;

        points.pop();

        drawFenceInfo.clonePoints = cloneDeep(points);
        handleStartDrawFence(true);

        setTimeout(() => {
          fenceInfo.visible = true;
          handleRedrawFence();
        }, 500);
      }
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function editRestore() {
      if (contextmenuTypeRef.value === TYPES.labelList) return;
      const { x, y } = activeTarget.attrs;
      (currentContextmenuRef.value as Shape).to({
        x,
        y,
      });
      if (contextmenuTypeRef.value === TYPES.camera) {
        const { direction } = activeTarget.attrs;
        (currentContextmenuRef.value as Image).image(cameraImageObjRef.value[direction]);
      }
      batchDraw();
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function clearShadowColor() {
      // if (!currentContextmenuRef.value) return;
      // currentContextmenuRef.value.shadowColor("rgba(0,0,0,0)");
      // editRestore();
    }

    /**
     * @description: 
     * @param {Event} e windowe
     * @return {*}
     */
    function showMenuList(e: MouseEvent) {
      const { clientX: x, clientY: y } = e;

      Object.assign(contxtmenuPosition, {
        left: x,
        top: y - titleHeight,
      });

      visibleObj.contextmenu = true;
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function hideMenuList() {
      if (visibleObj.contextmenu) {
        visibleObj.contextmenu = false;
        clearShadowColor();
        currentContextmenuRef.value = null;
      }
    }

    /**
     * @description: 
     * @param {object} imageAbout 
     * @param {function} func 
     * @param {string | number} type id ||  || 
     * @return {void}
     */
    function cacheImage(
      imageAbout: IImageAbout,
      func: Function,
      type: string | number = mapIdRef.value
    ) {
      let { imageObj } = imageAbout;
      const { src } = imageAbout;

      const isCached = !!(imageObj && (imageObj.src || imageObj[type]));

      let imageELe: Nullable<HTMLImageElement> = null;
      if (isCached) {
        imageELe = !(imageObj as IImageObj).src
          ? (imageObj as IImageObj)[type]
          : (imageObj as HTMLImageElement);
        func(imageELe);
      } else {
        imageELe = new window.Image() as HTMLImageElement;
        imageELe.src = src;
        (imageELe as HTMLImageElement).onload = () => {
          imageObj === null
            ? (imageObj = imageELe)
            : ((imageObj as IImageObj)[type] = imageELe as HTMLImageElement);
          func(imageELe);
        };
        (imageELe as HTMLImageElement).onerror = () => {
          ElMessage.error("");
        };
      }
    }

    function handleStageWheel() {
      (stage.value as Stage).on("wheel", (e) => {
        // e = e.evt;
        const ev: WheelEvent = e.evt;

        // ts   wheelDelta 
        // const wheelDelta = ev.wheelDelta ? ev.wheelDelta : ev.deltaY * -40;
        const wheelDelta = ev.deltaY * -40;

        const rateX = (ev.clientX - state.sx) / state.scale;
        const rateY = (ev.clientY - state.sy) / state.scale;

        if (wheelDelta > 0) {
          state.scale += 0.05;
          state.scale = Number((state.scale + 0.05).toFixed(2));
          if (state.scale >= state.maxScale) {
            state.scale = state.maxScale;
          }
        } else {
          state.scale -= 0.05;
          state.scale = Number((state.scale - 0.05).toFixed(2));
          if (state.scale <= state.minScale) {
            state.scale = state.minScale;
          }
        }

        state.sx = ev.clientX - state.scale * rateX;
        state.sy = ev.clientY - state.scale * rateY;

        (stage.value as Stage).setAttrs({
          x: state.sx,
          y: state.sy,
          scaleX: state.scale,
          scaleY: state.scale,
        });

        if (status.isDrawFence) {
          handleStartDrawFence();
        }

        batchDraw();
      });
    }

    function handleStageMouseDown() {
      (stage.value as Stage).on("mousedown", (e) => {
        const ev = e.evt;

        if (status.isDrawFence) {
          clickTimerRef.value = window.setTimeout(() => {
            const pos = screenTocanvas({ x: ev.clientX, y: ev.clientY }, state);
            drawFenceInfo.points.push(pos);
            handleStartDrawFence();
          }, 300);

          return;
        }

        status.draggable = true;

        Object.assign(mousePos, {
          x: ev.clientX,
          y: ev.clientY,
        });
        (stage.value as Stage).container().style.cursor = "move";

        hideMenuList();
      });
    }

    function handleStageMouseUp() {
      (stage.value as Stage).on("mouseup mouseout", () => {
        if (status.draggable) {
          status.draggable = false;
          (stage.value as Stage).container().style.cursor = "default";
        }
      });
    }

    function handleStageMouseMove() {
      (stage.value as Stage).on(
        "mousemove",
        debounce((e) => {
          const mouseMoveFunc = () => {
            const ev: MouseEvent = e.evt;

            if (status.isDrawFence) {
              const { points } = drawFenceInfo;

              if (points.length >= 1) {
                if (points.length > 1) {
                  points.pop();
                }
                const pos = screenTocanvas({ x: ev.clientX, y: ev.clientY }, state);
                points.push(pos);
                handleStartDrawFence();
              }
              return;
            }
            if (!status.draggable) return;

            state.sx += ev.clientX - mousePos.x;
            state.sy += ev.clientY - mousePos.y;

            const maxSx = getOffset(
              {
                x: (stage.value as Stage).width() * 0.25,
                y: 0,
              },
              {
                x: 0,
                y: 0,
              },
              state.scale
            ).x;
            const minSx = getOffset(
              {
                x: (stage.value as Stage).width() * 0.75,
                y: 0,
              },
              {
                x: bgImageConfig.width,
                y: 0,
              },
              state.scale
            ).x;
            const maxSy = getOffset(
              {
                x: 0,
                y: titleHeight,
              },
              { x: 0, y: 0 },
              state.scale
            ).y;
            const minSy = getOffset(
              {
                x: 0,
                y: (stage.value as Stage).height(),
              },
              { x: 0, y: bgImageConfig.height },
              state.scale
            ).y;

            const midSy = getOffset(
              {
                x: 0,
                y: (stage.value as Stage).height() / 2,
              },
              {
                x: 0,
                y: bgImageConfig.height / 2,
              },
              state.scale
            ).y;

            if (maxSx <= minSx) {
              state.sx = maxSx;
            } else if (state.sx > maxSx) {
              state.sx = maxSx;
            } else if (state.sx < minSx) {
              state.sx = minSx;
            }

            if (maxSy <= minSy) {
              state.sy = maxSy;
            } else if (state.sy > maxSy) {
              state.sy = maxSy;
            } else if (state.sy < minSy) {
              state.sy = minSy;
            }

            if (state.scale === state.minScale) {
              state.sy = midSy;
            }

            Object.assign(mousePos, {
              x: ev.clientX,
              y: ev.clientY,
            });

            (stage.value as Stage).setAttrs({
              x: state.sx,
              y: state.sy,
            });

            batchDraw();
          };
          mouseMoveFunc();
        }, 10)
      );
    }

