    const cacheBubbleObj: ICacheBubble = {};
    async function initBubble(attrs: TVectorgraph) {
      const offsetX = 24;
      const offsetY = 16;
      const OffsetX = 2 * offsetX;
      const OffsetY = 2 * offsetY;
      const { x, y, sname, lids } = attrs;

      const { W, H, layinfo, Color, IsHaveCount, CountItem } = attrs.defdata;
      const w = W - OffsetX;
      const h = H - OffsetY;
      // konva-test-vue
      const group = new Konva.Group({
        x,
        y,
      });

      const config: ISvgToImageConfig = {
        pathSrc: layinfo,
        width: W,
        height: H,
        color: Color,
      };

      const imageDom = await svgToImage(config, attrs.ltype === "");
      const image = new Konva.Image({
        image: imageDom,
        stroke: "rgba(255,0,0,.5)",
      });

      if (!IsHaveCount) {
        const label = new Konva.Text({
          x: W / 2 - offsetX,
          y: (h - 10) / 2,
          text: sname,
          fontSize: 14,
          fill: "#fff",
        });
        label.offsetX(label.width() / 2);
        label.offsetY(label.height() / 2);
        group.add(image).add(label);
      } else {
        const label = new Konva.Text({
          x: W / 2 - offsetX,
          y: H / 8,
          text: sname,
          fontSize: 14,
          fill: "#fff",
        });
        label.offsetX(label.width() / 2);

        const line = new Konva.Line({
          points: [w * 0.1, h / 2, w * 0.9, h / 2],
          stroke: "#fff",
          strokeWidth: 0.5,
        });

        const bottomGroup = new Konva.Group({
          x: w / 2,
          y: (h - 10) * 0.7,
        });

        cacheBubbleObj[lids] = [];

        const len = CountItem.length;
        // const len = 2;
        const gap = 10;

        CountItem.forEach((item, index) => {
          const cornerGroup = new Konva.Group({
            x: gap * index,
          });

          const cornerH = ((h - 10) / 2) * 0.5;
          const cornerRect = new Konva.Rect({
            width: w / 3 / len,
            height: cornerH,
            fill: "rgba(255,255,255,.6)",
            cornerRadius: cornerH,
          });
          cornerRect.offsetX(cornerRect.width() / 2);

          const cornerText = new Konva.Text({
            x: 0,
            y: cornerH / 2,
            fill: Color,
            text: "?",
            name: item,
          });

          cornerText.offsetX(cornerText.width() / 2);
          cornerText.offsetY(cornerText.height() / 2);

          cornerGroup.add(cornerRect).add(cornerText);

          cacheBubbleObj[lids].push(cornerText);

          bottomGroup.add(cornerGroup);
        });

        group.add(image).add(label).add(line).add(bottomGroup);

        group.on("dblclick", (e) => {
          console.log(e);
          const { clientX, clientY } = e.evt;
          Object.assign(layerPosition, {
            left: clientX,
            top: clientY - titleHeight,
          });
          visibleObj.layerPerson = true;
        });
      }

      allGroup.locateBubbleGroup.add(group);
    }

    const cacheAreaObj: ICachePerson = {};
    // 
    function initArea(attrs: IAreaItem) {
      const { areaids, points, areaname, show } = attrs;
      const posArr = JSON.parse(points) as IUpperPos[];
      const group = new Konva.Group({
        name: areaids,
        visible: show,
      });
      const startPoint = posArr[0];
      const text = new Konva.Text({
        x: startPoint.X + 10,
        y: startPoint.Y + 10,
        text: areaname,
        fill: "#fff",
        fontSize: 24,
      });
      const areaItem = new Konva.Shape({
        ...areaColorConfig,
        sceneFunc: function (ctx, shape) {
          ctx.beginPath();
          ctx.moveTo(posArr[0].X, posArr[0].Y);
          for (let i = 1; i < posArr.length; i++) {
            const { X, Y } = posArr[i];
            ctx.lineTo(X, Y);
          }
          ctx.stroke();
          ctx.closePath();
          ctx.fillStrokeShape(shape);
        },
      });
      group.add(areaItem).add(text);
      //   
      if (cacheAreaObj[areaids]) {
        cacheAreaObj[areaids].destroy();
        delete cacheAreaObj[areaids];
      }
      allGroup.fenceGroup.add(group);
      cacheAreaObj[areaids] = group;

      batchDraw();
    }

