    const cachePersonObj: ICachePerson = {};
    async function initPerson(attrs: IPersonArrItem, initType = 0) {
      const { x, y, name, id, type } = attrs;
      const generate = (img: HTMLImageElement) => {
        const group = new Konva.Group({
          name: id + "",
