        const image = new Konva.Image({
          x: 0,
          y: 0,
          width: img.width,
          height: img.height,
          image: img,
          shadowColor: "rgba(0,0,0,0)",
          shadowBlur: img.width,
          id: String(id), //   shapegroup  parent
        });

        const fontSize = 14;
        const text = new Konva.Text({
          x: 0,
          y: -fontSize - 5,
          text: name,
          fontSize,
          fill: "#fff",
          id: String(id),
        });

        group.add(image).add(text);

        cachePersonObj[id] = group;

        // 
        if (allGroup.personGroup.value && initType === 0) {
          allGroup.personGroup.add(group);
        } else if (operationLayer && initType) {
          operationLayer.add(group);
          group.moveToTop();
        }

        batchDraw();

        if (initType === 0) {
          group.on("contextmenu", (e) => {
            console.log(e);
            console.log(e.target.attrs.id);
            if (status.isDrawFence) {
              return false;
