            activeTarget.id = e.target.attrs.id;
            activeTarget.attrs = cloneDeep(e.target.attrs);
            hideDetails();

            currentContextmenuRef.value = image;
            contextmenuTypeRef.value = TYPES.labelList;
            showMenuList(e.evt);
            visibleObj.contextmenu = true;
            image.shadowColor("#123456");
            batchDraw();
          });
        } else {
          status.isPlayBack = true;
          // this.trackPlaybackTween(group, this.trackPlayback.steps)
        }
      };

      cacheImage(
        {
          src: `/images/staff_unchecked${type || 1}.png`,
          imageObj: personImageObjRef.value,
        },
        generate,
        type || 1
      );
    }

