    // /  
    function handleSubmitFence() {
      (fenceInfoFormRef.value as IELForm).validate(async (valid: boolean) => {
        if (valid) {
          const chooseDevice = fenceInfo.form.chosenCamera.map((item: string) => {
            const index = fenceInfo.allMapCamera.findIndex((i: any) => i.dids === item);
            return {
              DeviceIdS: item,
              DeviceType: fenceInfo.allMapCamera[index].dname,
            };
          });
          const params: IAddEditFenceParams = {
            act: "i9YWieOBzym06JamZtI1586422307190",
            LayerIdS: fenceInfo.dialogType === 0 ? guid() : editFenceId,
            SceneNum: mapIdRef.value,
            CheckSourceID: JSON.stringify(
              drawFenceInfo.clonePoints.map((item) => ({ X: item.x, Y: item.y }))
            ),
            DevicePointRelation:
              fenceInfo.form.chosenCamera.length === 0
                ? ""
                : JSON.stringify(chooseDevice),
            CheckSourceUnitName: fenceInfo.form.name,
          };
          const res: unknown = await stageApi.saveHArea(params);
          const { result } = res as IResponseCommon<string>;
          if (result.msg === "") {
            ElMessage.success(result.msg);
            fenceInfo.visible = false;
            getAllHArea();
          }
        }
      });
    }

