    async function handleEditRule(row: IRuleItem) {
      openRule(1);
      const { stime, ptype, memo, id, etime, isloop, limtime } = row;
      ruleId = id;

      const dateTime = (loop: boolean, time1: string, time2: string) => {
        if (loop) {
          // 
          const ymd = dayjs(new Date()).format("YYYY-MM-DD");
          return {
            startTime: new Date(`${ymd} ${time1}`),
            endTime: new Date(`${ymd} ${time2}`),
          };
        } else {
          return {
            startTime: new Date(time1),
            endTime: new Date(time2),
          };
        }
      };

      const { startTime, endTime } = await dateTime(isloop, stime, etime);

      ruleItemDetail.form.memo = memo;
      ruleItemDetail.form.isLoop = isloop;
      ruleItemDetail.form.limitedTime = String(limtime);
      ruleItemDetail.form.startTime = startTime;
      ruleItemDetail.form.endTime = endTime;
      ruleItemDetail.form.permitType = String(ptype);
      console.log(ruleItemDetail.form);

      // ? 
      // const obj = {
      //   memo,
      //   isLoop: isloop,
      //   limitedTime: limtime,
      //   startTime,
      //   endTime,
      //   permitType: String(ptype),
