    function addEquipToScene(attrs: { x: number; y: number; type: string | number }) {
      const { x, y, type } = attrs;
      const images = {
        [TYPES.basestation]: basestationImageObjRef.value,
        [TYPES.camera]: cameraImageObjRef.value[defaultCameraType],
      };
      const imgObj = images[type];

      const node = new Konva.Image({
        x: x - equipIconConfig.width / 2,
        y: y - equipIconConfig.height,
        width: equipIconConfig.width,
        height: equipIconConfig.height,
        name: ADD_EQUIP,
        image: imgObj as HTMLImageElement,
      });
      node.on("click", popEquipment);
      node.shadowColor("blue");
      node.shadowBlur(20);

      allGroup[`${contextmenuTypeRef.value}Group`].add(node);
      batchDraw();
      return node;
    }

    function putEquipment(e: KonvaEventObject<MouseEvent>) {
      const ev = e.evt;
      if (!status.equipImgFollow) return;
      const { x, y } = canvasToStage({
        x: ev.clientX,
        y: ev.clientY,
        ...state,
      });

      // todo x y 
      // ?   < 0  stage
      detailsForm.x = x;
      detailsForm.y = y;

      equipmentInfo.checkedNode = addEquipToScene({
        type: contextmenuTypeRef.value,
        x,
        y,
      });
      status.equipImgFollow = false;

      batchDraw();

      const dom = equipImgRef.value as HTMLElement;
      dom?.children?.[0] && dom.removeChild(dom.children[0]);
    }

    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    function equipImgFollow(e: KonvaEventObject<MouseEvent>) {
      const ev = e.evt;
      if (status.equipImgFollow === false) return;
      const { x, y } = canvasToStage({
        x: ev.clientX,
        y: ev.clientY,
        ...state,
      });

      detailsForm.x = x;
      detailsForm.y = y;

      const equipImgDom = equipImgRef.value as HTMLElement;
      const { offsetWidth: width, offsetHeight: height } = equipImgDom;
      equipImgDom.style.left = ev.clientX - width / 2 + "px";
      equipImgDom.style.top = ev.clientY - height - 2 + "px";
    }
