      if (detailsForm.isAdd) {
        node = (stage.value as Stage).findOne("." + ADD_EQUIP);
      } else {
        node = currentContextmenuRef.value as Shape;
      }
      if (x !== undefined) {
        node.to({
          x: x as number,
        });
      }

      if (y !== undefined) {
        node.to({
          y: y as number,
        });
      }
    }
