    // ws 
    //  ws  
    let wsData: any[] = [];
    let locationWs: Nullable<WebSocket> = null;
    function initLocationWs() {
      // const url = "WS://192.168.1.248:38559/location";
      const url = wsUrl.location;
      locationWs = new WebSocket(url);
      locationWs.onmessage = async (event) => {
        const data = JSON.parse(event.data);
        // console.log(data);

        if (data.ActionCode === EpauletActionCode.HTagInit) {
          person.all =
            data.Data.map((item: any) => {
              const { x, y } = strToPos(item.LocationPoint);
              return {
                ...item,
                x,
                y,
                id: item.TagId,
                name: item.ObjData.BindObjName,
              };
            }) || [];
          // console.log("person.all", person.all);

          getCurrentMapPerson(mapIdRef.value);
        }

        if (data.ActionCode === EpauletActionCode.HTagOnline) {
          const { x, y } = strToPos(data.Data.LocationPoint);
          const posItem = {
            ...data.Data,
            x,
            y,
          };
          person.all.push(posItem);
          if (mapIdRef.value === data.Data.LocationSceneId) {
            person.currentMap.push(posItem);
            initPerson(posItem);
          }
          ElMessage.info(`${posItem.ObjData.BindObjName}`);
        }

        if (
          data.ActionCode === EpauletActionCode.HTagOffline ||
          data.ActionCode === EpauletActionCode.HTagLogOff
        ) {
          const index = person.all.findIndex((item) => item.id === data.Data.TagId);
          if (index > -1) {
            person.all.splice(index, 1);
            if (data.Data.mapId === mapIdRef.value) {
              const currntMapIndex = person.currentMap.findIndex(
                (item) => item.id === data.Data.TagId
              );
              if (currntMapIndex > -1) {
                person.currentMap.splice(currntMapIndex, 1);
                const node = cachePersonObj[data.Data.TagId];
                node && node.destroy();
              }
            }
            ElMessage.info(`${data.Data.ObjData.BindObjName}`);
          }
        }

        if (data.ActionCode === EpauletActionCode.HTagMove) {
          // console.log(data.Data);

          const { x, y } = strToPos(data.Data.LocationPoint);
          const posObj = {
            id: data.Data.TagId,
            x,
            y,
          };
          const fn = () => {
            const index = person.all.findIndex((item) => item.id === posObj.id);
            if (index > -1) {
              Object.assign(person.all[index], posObj);
            }
          };
          fn();
          if (data.Data.LocationSceneId === mapIdRef.value) {
            wsData.push(posObj);
          }
        }

        if (data.ActionCode === EpauletActionCode.HLayerGroupStatistics) {
          // data.Data.forEach((item:any,index:number)=>{
          //   cacheBubbleObj[item.LayerIdS][index].text(String(item.TagDataList.length));
          //   // todo 
          // })
          batchDraw();
        }
      };

      // 
      const time = 200;
      const duration = time / 1000;
      const timer = window.setInterval(() => {
        console.log("length=>", wsData.length);

        wsData.forEach((item) => {
          const index = person.currentMap.findIndex((pos) => pos.id === item.id);
          if (index > -1) {
            const { x, y } = person.currentMap[index];
            if (!(x === item.x && y === item.y)) {
              Object.assign(person.currentMap[index], item);
              const node = cachePersonObj[item.id];
              node &&
                node.to({
                  x: item.x,
                  y: item.y,
                  duration: duration,
                });
              batchDraw();
            }
          }
        });
        wsData = [];
      }, time);

      locationWs.onclose = () => {
        window.clearInterval(timer);
      };
    }
