          const { data } = res as IResponseCommon<any>;
          console.log(data);
          // data[0][0].DeviceObjProperty
        } else {
          ElMessageBox.confirm("", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "info",
          })
            .then(() => {
              changeMap(mapNum);
            })
            .catch(() => {
              ElMessage.info("");
              leftPannelSearch.value = "";
            });
        }
      }
    }

    // 
    // window resize
    function handleWindowResize() {
      if (!cacheBgImage) return;
      state.scale = Number((window.innerWidth / cacheBgImage.width()).toFixed(2));
      state.minScale = state.scale;
      state.sx = 0;
      state.sy = 0;
      const attrs = {
        x: state.sx,
        y: state.sy,
        width: window.innerWidth,
        height: window.innerHeight - titleHeight,
        scale: {
          x: state.scale,
          y: state.scale,
        },
      };
      (stage.value as Stage).setAttrs(attrs);
      batchDraw();
    }

    const debounceRem = debounce(computedRem, 100);
    const debounceResize = debounce(handleWindowResize, 100);

    onMounted(() => {
      getAllMap();
      getAllAlarmType();
      computedRem();
      window.addEventListener("resize", debounceRem);
      window.addEventListener("resize", debounceResize);
    });

    onUnmounted(() => {
      clickTimerRef.value && clearTimeout(clickTimerRef.value);
      locationWs?.close();
      alarmWs?.close();
      window.removeEventListener("resize", debounceRem);
      document.documentElement.style.fontSize = "";
      window.removeEventListener("resize", debounceResize);
    });

    return {
      warningWidth,
      showWarningList,
      mapIdRef,
      mapNameRef,
      mapListRef,
      showDetails,
      formContentComponentRef,
      mainNavRef,
      visibleObj,
      status,
      drawerInfo,
      // drawerInfo: toRefs(drawerInfo),
      fenceInfo,
      fenceListInfo,
      ruleListInfo,
      ruleFormRef,

      ruleItemDetail,
      types: TYPES,
      navClick,
      resetNavIndex,
      equipSwitchClick,
      fenceInfoFormRef,
      handleAddFence,
      handleFenceDraw,
      handleEditFence,
      handleSubmitFence,
      handleDelFence,
      fenceCurrentChange,
      drawFenceInfo,
      handleRevokeFence,
      handleRedrawFence,
      handleCancelDrawFence,
      handleFinishDrawFence,
      contextmenuTypeRef,
      contxtmenuPosition,
      contextmenuList,
      detailsForm,
      trackPlayback,
      hideDetails,
      equipImgRef,
      addEquip,
      updateX,
      updateY,
      changeDirection,
      cancelForm,
      handleDetailConfirm,
      detailsEdit,
      handleClickTrackPlayback,
      handleCloseContextmenu,
      handleClickSeeTrackPlayback,
      seeNowVideo,
      seeHistoryVideo,
      handleCloseAllVideo,
      handleCloseAllHistoryVideo,
      popMonitorVideo,
      popHistoryMonitorVideo,
      watchMonitorListPanelState,
      watchHistoryMonitorListPanelState,
      monitorListInfo,
      showEquipDetails,
      alarmType,
      alarmForm,
      transferProps,
      transferData,
      handleSeeRules,
      handleAddRule,
      handleEditRule,
      handleSubmitRule,
      handleDelRule,
      areaRulesOption,
      ruleCurrentChange,
      ruleDisabledDate,
      timerPickerChange,
      ruleItemDetailRule,
      changeAreaVisible,
      toggleAllFenceVisible,
      layerPerson,
      layerPosition,
      controlButtonList,
      controlButtonChange,
      closeMapChoosen,
      changeMap,
      leftPannelSearch,
      querySearch,
      searchChange,
      searchDevice,
      person,
      userSearchRes,
      alarmInfo,
      alarmListInfo,
      formatTime,
      alarmCurrentChange,
      getAlarmListByCondition,
      handleDetailAlarm
    };
  },
});
</script>

