<template>
  <div>
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <el-form-item label="">
        <el-input v-model="params.search" clearable @change="search" />
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="tableData" border tooltip-effect="dark">
      <el-table-column
        label=""
        prop="DeviceName"
        show-overflow-tooltip
        align="center"
        width="110"
      />
      <el-table-column
        label=""
        prop="Username"
        show-overflow-tooltip
        align="center"
      />
      <el-table-column label="" show-overflow-tooltip align="center">
        <template #default="{row}">
          {{ getMap(row.snum) || "" }}
        </template>
      </el-table-column>
      <el-table-column width="160" label="" align="center">
        <template #default="{row}">
          <el-button size="mini" type="primary" @click="handleDetail(row)"
            ></el-button
          >
          <!-- <el-button size="mini" @click="handleEdit(row)">Edit</el-button> -->
          <!-- <el-popconfirm
            :title="` ${row.ip} `"
            @confirm="handleDelete(row)"
          >
            <template #reference>
              <el-button type="danger" size="mini" style="margin-left:10px;"
                ></el-button
              >
            </template>
          </el-popconfirm> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      :page-size="params.size"
      layout="prev, pager, next"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import { onMounted, reactive, ref, defineComponent } from "vue";
import api from "@/api/person";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IPersonItem } from "../types";
import { getPagination, getPagintionCount, getMap } from "@/utils";
const CLICKED = "clicked";

export default defineComponent({
  name: "Label",
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      size: 10,
      prop: "Epaulet",
      search: ""
    });

    const tableData = ref<IPersonItem[]>([]);
    const total = ref(0);

    async function getList() {
      const param = {
        act: "rYVwSSwhmDcZRL0yQbO1611304770982"
      };
      const result: unknown = await api.getPersonList(param);

      const { data } = result as any;
      const personAll: any = data;
      const res =
        params.search === ""
          ? getPagination(personAll, params.page)
          : getPagination(personAll, params.page, params.prop, params.search);
      tableData.value = res;
      total.value = getPagintionCount(
        personAll,
        params.prop,
        params.search
      ) as number;
    }

    async function handleEdit(row: IPersonItem) {
      console.log(row);
    }
    async function getPersonDetail(row: any) {
      emit("showEquipDetails", row, TYPES.person);
      emit(CLICKED);
    }
    function handleDetail(row: IPersonItem) {
      getPersonDetail(row);
    }
    async function handleDelete(row: IPersonItem) {
      console.log(row);
    }

    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      tableData,
      total,
      handleEdit,
      handleDelete,
      changePage,
      search,
      getMap,
      handleDetail
    };
  }
});
</script>
