export const areaRulesOption = areaRules.map((item, index) => ({
  label: item,
  value: String(index + 1)
}));

export const formateRuleType = (type: string | number) => {
  type = String(type);
  const index = areaRulesOption.findIndex(item => item.value === type);
  return areaRulesOption[index].label;
};

