import http from "@/utils/http3";

export default {
  // 
  getPersonData(id: string | number) {
    return http({
      url: `/person/criminal/${id}/`
    });
  },
  // 
  getPersonList(data: any) {
    return http({
      url: `/rcaec/bigwa`,
      method: "post",
      data
    });
  }
};
