<template>
  <div v-drag-dialog="`form-header,custom-form`">
    <div class="custom-form map-container">
      <div class="form-header">
        <div class="title"></div>
        <button class="closebtx" @click="handleClickHeaderClose">
          <i class="el-icon el-icon-close" />
        </button>
      </div>
      <div class="form-body">
        <div class="map-content">
          <div
            class="map-item"
            v-for="mapItem in mapList"
            :key="mapItem.snum"
            :class="{ active: mapItem.snum === mapId }"
            @click="changeMap(mapItem.snum)"
          >
            <img :src="imgUrl + mapItem.surl" alt="" />
            <span>
              {{ mapItem.sname }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import dragDialog from "../directive/drag-dialog";
import { imgUrl } from "@/utils/urls";

import { defineComponent, PropType } from "vue";
import { IMapArrItem } from "../types";

const CLOSED = "closed";
const ChangeMap = "changeMap";

export default defineComponent({
  name: "mapChoosen",
  directives: { "drag-dialog": dragDialog },
  props: {
    mapId: {
      type: String,
      required: true
    },
    mapList: {
      type: Object as PropType<IMapArrItem[]>,
      default: () => []
    }
  },
  emits: [CLOSED, ChangeMap],
  setup(props, { emit }) {
    function handleClickHeaderClose() {
      emit(CLOSED);
    }
    function changeMap(mapId: string) {
      if (mapId === props.mapId) return;
      emit(ChangeMap, mapId);
    }
    return {
      imgUrl,
      handleClickHeaderClose,
      changeMap
    };
  }
});
</script>

<style lang="scss" scoped>
.map-container {
  /* top: 50%;
  left: 50%;
  transform: translate(-50%, 0); */
}
.map-content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-height: 600px;
  overflow-y: scroll;
}
.map-item {
  width: 45%;
  margin: 2.5%;
  display: flex;
  flex-direction: column;
  max-height: 126px;
  padding: 2%;
  justify-content: center;
  align-items: center;
  background: url("../../../../assets/dashboard/equip_unchecked.png") repeat
    center center/100% 100%;
  overflow: hidden;
  transition: color 0.3s ease-in-out;
  cursor: pointer;
  img {
    width: 96%;
  }
  span {
    display: inline-block;
    margin-top: 10px;
    font-size: 14px;
  }
  &.active,
  &:hover {
    background-image: url("../../../../assets/dashboard/important_monitor.png");
    color: #3ed2ff;
  }
}
</style>
