
import { defineComponent, reactive, ref, onMounted, shallowRef } from "vue";
import api from "@/api/camera";
import { TYPES } from "../constVariables";
import { ICommonTableParams, ICameraArrItem } from "../types";
import { getPagination, getPagintionCount, getMap } from "@/utils";
import { ElMessage } from "element-plus";
// import { parse } from "qs";
import { getAllDevice } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "Camera",
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      size: 10,
      prop: "dnum",
      search: ""
    });
    const tableData = ref<ICameraArrItem[]>([]);
    const total = shallowRef(0);

    async function getList() {
      // const res: unknown = await api.getCameraList(params);
      // localStorage
      const deviceAll: any = getAllDevice();
      const cameraAll: any = [];
      deviceAll.filter((item: any) => {
        if (item.dtypecode === "Camera_IP") {
          cameraAll.push(item);
        }
      });
      const res =
        params.search === ""
          ? getPagination(cameraAll, params.page)
          : getPagination(cameraAll, params.page, params.prop, params.search);
      tableData.value = res;
      total.value = getPagintionCount(
        cameraAll,
        params.prop,
        params.search
      ) as number;
    }

    async function handleEdit(row: ICameraArrItem) {
      console.log(row);
    }

    async function getCameraDetail(id: string) {
      const params = {
        act: "fCGtP4nXdRZjr6TYeiN1611196242913",
        DeviceIdS: id
      };

      const res: unknown = await api.getCameraData(params);
      const { data } = res as any;
      emit("showEquipDetails", data[0][0], TYPES.camera);
      emit(CLICKED);
    }
    function handleDetail(row: ICameraArrItem) {
      getCameraDetail(row.dids);
    }

    function handleDelete(index: number, row: ICameraArrItem) {
      api
        .delCamera(row.dids)
        .then(() => {
          ElMessage.success("");
        })
        .catch(err => {
          ElMessage.error(err);
        });
    }

    async function changePage(page: number) {
      params.page = page;
      getList();
    }

    async function search() {
      params.page = 1;
      getList();
    }

    function addEquip() {
      emit("addEquip", TYPES.camera);
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      tableData,
      total,
      handleEdit,
      handleDelete,
      changePage,
      search,
      addEquip,
      getMap,
      handleDetail
    };
  }
});
