<template>
  <div v-drag-dialog="`form-header,custom-form`">
    <div class="custom-form">
      <div class="form-header">
        <div class="title">{{ formTitle }}</div>
        <button class="closebtx" @click="handleClickHeaderClose">
          <i class="el-icon el-icon-close" />
        </button>
      </div>
      <div class="form-body">
        <el-form
          ref="formContentRef"
          :model="form"
          :rules="rules"
          label-width="110px"
          :inline="false"
          size="small"
        >
          <!--  -->
          <el-row v-if="formType === types.labelList" :gutter="10">
            <el-col :span="12" :offset="0">
              <el-form-item label="" prop="shapeName">
                {{ form.DeviceName }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0"
              ><el-form-item label="" prop="roomNum">
                {{ form.DeviceNum }}
              </el-form-item></el-col
            >
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.DeviceType }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.SceneName || "" }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.IsUse ? "" : "" }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.shapeName }}
              </el-form-item>
            </el-col>
          </el-row>
          <!--  -->
          <el-row v-else-if="formType === types.person" :gutter="10">
            <el-col :span="12" :offset="0">
              <el-form-item label="" prop="shapeName">
                {{ form.Username }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0"
              ><el-form-item label="" prop="roomNum">
                {{ form.DeviceName ? form.DeviceName : "" }}
              </el-form-item></el-col
            >
            <el-col :span="24" :offset="0"
              ><el-form-item label="">
                <el-image
                  :src="form.Photo ? form.Photo : form.src"
                /> </el-form-item
            ></el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.Personnel_category }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.Position }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.Fettle === 0 ? "" : "" }}
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="">
                {{ form.BraceletVoltage }}
              </el-form-item>
            </el-col>
          </el-row>
          <!--  -->
          <el-row v-else-if="formType === types.basestation" :gutter="10">
            <el-col :span="24" :offset="0">
              <el-form-item label="" prop="shapeName">
                <el-input
                  v-if="editable"
                  v-model.trim="form.DeviceName"
                  placeholder=""
                  clearable
                />
                <template v-else>{{ form.DeviceName }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="24" :offset="0">
              <el-form-item label="" prop="ip">
                <el-input
                  v-if="editable"
                  v-model.trim="form.DeviceNum"
                  placeholder=""
                  clearable
                />
                <template v-else>{{ form.DeviceNum }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="x" prop="x">
                <el-input
                  v-if="editable"
                  v-model.trim="form.x"
                  placeholder="x"
                  clearable
                />
                <template v-else>{{ form.x }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="y" prop="y">
                <el-input
                  v-if="editable"
                  v-model.trim="form.y"
                  placeholder="y"
                  clearable
                />
                <template v-else>{{ form.y }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="24" :offset="0">
              <el-form-item label="" prop="type">
                <el-select
                  v-if="editable"
                  v-model="form.DeviceType"
                  clearable
                  placeholder=""
                >
                  <el-option label="" value="0" />
                  <el-option label="" value="1" />
                </el-select>
                <template v-else>{{ form.DeviceType }}</template>
              </el-form-item>
            </el-col>
          </el-row>
          <!--  -->
          <el-row v-else :gutter="10">
            <el-col :span="24" :offset="0">
              <el-form-item label="" prop="shapeName">
                <el-input
                  v-if="editable"
                  v-model.trim="form.DeviceName"
                  placeholder=""
                  clearable
                />
                <template v-else>{{ form.DeviceName }}</template>
              </el-form-item>
            </el-col>
            <!-- <el-col :span="24" :offset="0">
              <el-form-item label="" prop="direction">
                <el-select
                  v-if="editable"
                  v-model="form.direction"
                  placeholder=""
                  @change="changeDirection"
                >
                  <el-option
                    v-for="item in cameraDirection"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  />
                </el-select>

                <template v-else>{{
                  cameraDirectionFormate(form.direction)
                }}</template>
              </el-form-item>
            </el-col> -->
            <el-col :span="24" :offset="0">
              <el-form-item label="ip" prop="DeviceNum">
                <el-input
                  v-if="editable"
                  v-model.trim="form.DeviceNum"
                  placeholder="ip"
                  clearable
                />
                <template v-else>
                  {{ form.DeviceNum }}
                </template>
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="x" prop="x">
                <el-input
                  v-if="editable"
                  v-model.trim="form.x"
                  placeholder="x"
                  clearable
                />
                <template v-else>{{ form.x }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0">
              <el-form-item label="y" prop="y">
                <el-input
                  v-if="editable"
                  v-model.trim="form.y"
                  placeholder="y"
                  clearable
                />
                <template v-else>{{ form.y }}</template>
              </el-form-item>
            </el-col>
            <el-col :span="12" :offset="0" />
          </el-row>

          <!--  -->
          <el-row :gutter="0">
            <el-col :span="24" :offset="0">
              <el-form-item label="" prop="desc">
                <el-input v-if="editable" v-model="form.desc" type="textarea" />
                <template v-else>{{ form.desc }}</template>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item>
            <!-- <el-button v-if="editable" type="primary" @click="handleClick"
              ></el-button
            > -->
            <!-- form  -->
            <!-- <el-button v-if="!editable" @click="$emit('close')"></el-button> -->
            <!--    -->
            <!-- <el-button v-else @click="$emit('cancelForm')"></el-button> -->
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import dragDialog from "../directive/drag-dialog";
import { cameraDirectionFormate } from "../filter";
import { TYPES, cameraDirection } from "../constVariables";
import { commonRuleItem, customFuncRule } from "@/utils";
import {
  computed,
  defineComponent,
  nextTick,
  PropType,
  reactive,
  ref,
  watch
} from "vue";
import { bgImageConfig } from "../constVariables";
import { TShapeType } from "../types";
import { IELForm } from "../types/element";

const UPDATE_X = "updateX";
const UPDATE_Y = "updateY";
const CONFIRM = "confirm";
const CHANGE_DIRECTION = "changeDirection";
const CANCEL_FORM = "cancelForm";
const CLOSE = "close";

export default defineComponent({
  name: "FormContent",
  directives: { "drag-dialog": dragDialog },
  props: {
    // 
    isAdd: {
      type: Boolean,
      default: false
    },
    formType: {
      type: String as PropType<TShapeType>,
      default: TYPES.person
    },
    editable: {
      type: Boolean,
      default: false
    },
    x: { type: Number, default: 0 },
    y: { type: Number, default: 0 },
    formData: {
      type: Object,
      default: () => ({})
    },
    mapId: {
      type: [String, Number],
      required: true
    }
  },
  emits: [UPDATE_X, UPDATE_Y, CONFIRM, CHANGE_DIRECTION, CANCEL_FORM, CLOSE],
  setup(props, { emit }) {
    const formContentRef = ref();

    const form = reactive({
      // 
      roomNum: "1",
      src: "/images/staff_big.png",
      // 
      ip: "10.1.200.16",
      x: "",
      y: "",
      type: "",

      AlarmMappingLayerIdS: "",
      BrandCode: "",
      BrandName: "",
      BuildingName: "",
      BuildingNum: "",
      CreateTime: "",
      DeviceName: "",
      DeviceNum: "",
      DeviceObjProperty: "",
      DeviceSelfPointRelation: "",
      DeviceType: "",
      DeviceTypeCode: "",
      InstallSceneNum: "",
      IsUse: true,
      OwnAlarmSourceCode: "",
      PDeviceIds: "",
      SceneName: "",

      // 
      direction: "1",
      // 
      shapeName: "",
      desc: ""
    });

    const checkIp = (rule: any, value: string, callback: Function) => {
      if (!value) {
        return callback(new Error("ip"));
      }
      const pattern = /((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}/g;
      if (pattern.test(value)) {
        callback();
      } else {
        return callback(new Error("ip"));
      }
    };

    const checkPos = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      if (!value) {
        return callback(new Error(""));
      }
      value = Number(value);
      if (!Number.isInteger(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    const checkPosX = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      const w = bgImageConfig.width;
      if (value >= w) {
        callback(new Error(`${w - 1}`));
      } else if (value < 0) {
        callback(new Error(`x0`));
      } else {
        emit(UPDATE_X, value);
        callback();
      }
    };
    const checkPosY = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      const h = bgImageConfig.height;
      if (value >= h) {
        callback(new Error(`${h - 1}`));
      } else if (value < 0) {
        callback(new Error(`y0`));
      } else {
        emit(UPDATE_Y, value);
        callback();
      }
    };

    const rules = {
      shapeName: [commonRuleItem("")],
      ip: [customFuncRule(checkIp)],
      x: [customFuncRule(checkPos), customFuncRule(checkPosX)],
      y: [customFuncRule(checkPos), customFuncRule(checkPosY)],
      type: [commonRuleItem("")],
      direction: [commonRuleItem("")],
      DeviceNum: [commonRuleItem("ip")]
    };
    const types = TYPES;

    const formTitle = computed(() => {
      const title = {
        [TYPES.camera]: "",
        [TYPES.basestation]: "",
        [TYPES.labelList]: "",
        [TYPES.person]: ""
      };
      return title[props.formType];
    });

    /**
     * @description: 
     * @param {*} direction
     * @return {*}
     */
    function changeDirection(direction: string) {
      emit(CHANGE_DIRECTION, direction);
    }
    function handleClickHeaderClose() {
      const emitName = props.editable ? CANCEL_FORM : CLOSE;
      emit(emitName);
    }

    function resetFields() {
      (formContentRef.value as IELForm).resetFields();
    }

    function handleClick() {
      (formContentRef.value as IELForm).validate((valid: boolean) => {
        if (valid) {
          //  form
          emit(CONFIRM, form);
        }
      });
    }

    watch(
      () => props.formData,
      newVal => {
        nextTick(() => {
          if (newVal) {
            console.log("newVal", newVal);
            Object.assign(form, newVal);
            form.x = String(props.x);
            form.y = String(props.y);
          }
        });
      }
    );

    watch(
      () => props.x,
      newX => {
        console.log("x", newX);
        if (!isNaN(newX)) {
          form.x = String(newX);
        }
      }
    );
    watch(
      () => props.y,
      newY => {
        if (!isNaN(newY)) {
          form.y = String(newY);
        }
      }
    );

    // ?  y0
    // watch([() => props.x, () => props.y], ([newX], [newY]) => {
    //   console.log(newX);
    //   console.log(newY);

    //   if (!isNaN(newX)) {
    //     form.x = String(newX);
    //   }
    //   if (!isNaN(newY)) {
    //     form.y = String(newY);
    //   }
    // });

    return {
      formContentRef,
      cameraDirectionFormate,
      formTitle,
      form,
      rules,
      cameraDirection,
      changeDirection,
      handleClickHeaderClose,
      types,
      resetFields,
      handleClick
    };
  }
});
</script>
