-->
<template>
  <div class="handle" @scroll.capture="lazyLoad($event)">
    <div class="handle-class">
      <button
        :class="flag == '' ? 'active' : ''"
        @click="handleStatus('')"
        @touchstart.stop.prevent="handleStatus(0)"
      >
        
      </button>
      <button
        :class="flag == '' ? 'active' : ''"
        @click="handleStatus('')"
        @touchstart.stop.prevent="handleStatus(2)"
      >
        
      </button>
    </div>
    <div class="danger-level">
      <button
        :class="status[3]"
        @click="handleLevel(3)"
        @touchstart.stop.prevent="handleLevel(3)"
      >
        
      </button>
      <button
        :class="status[2]"
        @click="handleLevel(2)"
        @touchstart.stop.prevent="handleLevel(2)"
      >
        
      </button>
      <button
        :class="status[1]"
        @click="handleLevel(1)"
        @touchstart.stop.prevent="handleLevel(1)"
      >
        
      </button>
      <button
        :class="status[0]"
        @click="handleLevel(0)"
        @touchstart.stop.prevent="handleLevel(0)"
      >
        
      </button>
    </div>

    <ul ref="alarmBarRef" name="warn-items" @touchmove="scrollAlarms">
      <li
        v-for="(item, index) in showAlarms"
        :key="item.Id"
        ref="itemsRef"
        class="active"
      >
        <div
          class="top-bar"
          :style="item.status == true ? 'filter:grayscale(0.5)' : ''"
        >
          <span :style="styleArr[item.alarmLevel]" class="alarm-level">{{
            alarmLevel(item.alarmLevel)
          }}</span>
          <p>
            <span style="line-height:2.6rem">
              {{ formatTime(item.CreateTime) }}
            </span>
            &nbsp;-
            <span>
              {{ item.SourceType }}
            </span>
            <br />
            <span :style="colorStyleArr[item.alarmLevel]">{{
              item.alarmContent
            }}</span>
          </p>
        </div>
        <div class="bot-bar">
          <el-button
            size="mini"
            v-show="item.map"
            @click="viewToAlarm(item)"
            @touchstart.stop.prevent="viewToAlarm(item)"
          >
            
          </el-button>
          <el-button
            size="mini"
            @click="alarmDetail(item.id)"
            @touchstart.stop.prevent="alarmDetail(item.id)"
          >
            
          </el-button>
          <el-popconfirm
            title=" "
            @confirm="handleAlarm(item.id, index, item.dispose_status)"
          >
            <template #reference>
              <el-button size="mini" v-show="item.dispose_status !== 2">
                {{ item.dispose_status === 0 ? "" : "" }}
              </el-button>
            </template>
          </el-popconfirm>
        </div>
      </li>
      <li v-show="!loading" class="tips"><p></p></li>
    </ul>
    <div class="set-bar">
      <el-popconfirm title=" " @confirm="handleAll">
        <template #reference>
          <el-button size="mini" v-show="flag != ''" class="single-line">
            
          </el-button>
        </template>
      </el-popconfirm>
      <span>{{ total }}</span>
      <!-- --------------- -->
      <div>
        <span v-show="loading" class="loader loader--style2" title="1">
          <svg
            version="1.1"
            id="loader-1"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            x="0px"
            y="0px"
            width="40px"
            height="40px"
            viewBox="0 0 50 50"
            style="enable-background:new 0 0 50 50;"
            xml:space="preserve"
          >
            <path
              fill="#000"
              d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z"
            >
              <animateTransform
                attributeType="xml"
                attributeName="transform"
                type="rotate"
                from="0 25 25"
                to="360 25 25"
                dur="0.6s"
                repeatCount="indefinite"
              />
            </path>
          </svg>
        </span>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  reactive,
  ref,
  toRefs,
  onMounted,
  watch,
  nextTick
} from "vue";
import api from "@/api/alarm";
import { formatTime } from "@/utils";
import { ElMessage } from "element-plus";
// import { ElMessage } from "element-plus";

export default defineComponent({
  name: "Handle",
  props: {
    alarmType: {
      required: false,
      default: ""
    }
  },
  setup(props) {
    const params = reactive({
      status: ["", "", "", "active"],
      showAlarms: [] as any,
      alarms: [],
      styleArr: [
        { background: "#00A67B" },
        { background: "#FF7352" },
        { background: "#D9354E" }
      ],
      colorStyleArr: [
        { color: "#00A67B" },
        { color: "#FF7352" },
        { color: "#D9354E" }
      ],
      flag: "",
      size: 10,
      page: 1,
      loading: false,
      oldY: undefined,
      disposeStatus: "", // 
      typeid: "", // id
      alarmLevel: "" as string | number,
      alarmTypeArr: [], // 
      alarmTypeName: "",
      total: 0,
      contentVisiable: false // 
    });

    const itemsRef = ref();
    const alarmBarRef = ref();
    const dataRef = toRefs(params);
    async function getAlarmList() {
      const param = {
        act: "6Z6wTPGkfMVF9eLTOXA1611040899987",
        curpage: params.page,
        // eslint-disable-next-line @typescript-eslint/camelcase
        sys_showpagesize: params.size,
        ShowSceneNum: "",
        SourceType: props.alarmType,
        alarmLevel: params.alarmLevel,
        sTime: "",
        eTime: "",
        alarmProgress: params.disposeStatus
      };
      const res = await api.getAllAlamList(param);
      const { result, data } = res as any;

      if (result.success) {
        params.showAlarms = [...params.showAlarms, ...data.content];
        params.total = data.count;
      }
      params.loading = false;
    }

    // 
    function alarmLevel(n: number) {
      const arr = ["", "", ""];
      return arr[n];
    }

    function scrollAlarms(e: any) {
      const newY = e.targetTouches[0].pageX;
      if (params.oldY === undefined) {
        params.oldY = newY;
      }
      const y = params.oldY === undefined ? newY : params.oldY;
      alarmBarRef.value.scrollTop -= newY - y;
      params.oldY = newY;
    }
    // 
    function handleStatus(count: string) {
      params.loading = true;
      if (params.disposeStatus == count) {
        params.loading = false;
        return;
      }
      params.page = 1;
      params.flag = count;
      params.disposeStatus = count;
      params.showAlarms = [];
      getAlarmList();
    }
    // 
    function lazyLoad(e: any) {
      const itemLength = itemsRef.value && itemsRef.value.offsetHeight;
      const ulLength = alarmBarRef.value.offsetHeight;
      if (
        e.target.scrollTop >
        itemLength * params.page * params.size - ulLength
      ) {
        if (params.showAlarms.length < params.page * params.size) {
          return;
        }

        params.page += 1;
        params.loading = true;
        getAlarmList();
      }
    }
    // 
    function handleLevel(count: number) {
      params.loading = true;
      if (
        params.alarmLevel === count ||
        (params.alarmLevel === "" && count === 3)
      ) {
        params.loading = false;
        return;
      }
      params.status = ["", "", "", ""];
      params.status[count] = "active";
      if (count === 3) {
        params.alarmLevel = "";
      } else {
        params.alarmLevel = count;
      }
      params.page = 1;
      params.showAlarms = [];
      getAlarmList();
    }

    // 
    function alarmDetail(id: string | number) {
      console.log(id);
      params.contentVisiable = true;
    }

    // 
    function handleAlarm(id: string | number, index: number) {
      // const res = api.handleAlarmData;
      // const { data } = res as any;
      // getAlarmList();
      params.showAlarms.splice(index, 1);
      params.total = params.total - 1;
      ElMessage.success("!");
    }

    //
    function handleAll() {
      params.showAlarms = [];
      // const res = api.dealAlarms;
      // const { data } = res as any;
      // getAlarmList();
      ElMessage.success("!");
    }

    onMounted(() => {
      getAlarmList();
    });

    watch(
      () => props.alarmType,
      val => {
        nextTick(() => {
          params.page = 1;
          params.showAlarms = [];
          getAlarmList();
        });
      }
    );
    return {
      ...dataRef,
      itemsRef,
      alarmBarRef,
      formatTime,
      alarmLevel,
      scrollAlarms,
      handleStatus,
      lazyLoad,
      handleLevel,
      alarmDetail,
      handleAll,
      handleAlarm
    };
  }
});
</script>

<style lang="scss" scoped>
button {
  border: 0;
  background: 0;
  cursor: pointer;
  outline: none;
}
.handle {
  width: 98%;
  min-width: 220px;
  height: 100vh;
  color: #fff;
  position: relative;
  background: rgb(9, 29, 75, 0.5);
  opacity: 0.95;
  .handle-class {
    display: flex;
    justify-content: space-around;
    button {
      flex-grow: 1;
      line-height: 4rem;
      font-size: 1.6rem;
      background: #23406f;
      color: #fff;
      padding: 0 0.6rem;
      cursor: pointer;
      border-radius: 0;
      width: 100%;
    }
    .active {
      background: 0;
      font-size: 1.6rem;
      font-family: Microsoft YaHei;
      font-weight: 400;
      color: rgba(61, 209, 255, 1);
      line-height: 2rem;
    }
  }
  .danger-level {
    display: flex;
    justify-content: space-between;
    padding: 0rem 0rem 0rem 1rem;
    /* width: 98%; */
    button {
      line-height: 3rem;
      margin: 1rem 0;
      color: #ffffff;
      background: #3672c3;
      border-radius: 0.3rem;
      padding: 0 1.9rem;
      font-size: 1.4rem;
      font-family: Microsoft YaHei;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      span {
        margin-left: 1.5rem;
      }
    }
    .active {
      background: #3dd1ff;
    }
  }
  .alarm-type {
    display: flex;
    font-size: 1.4rem;
    align-items: center;
    padding-left: 1rem;
    padding-bottom: 1rem;
    .spinner-item {
      width: 17rem;
      min-width: 120px;
    }
  }
  ul {
    // border-bottom: 1px solid rgba(134, 208, 255, 0.2);
    height: 70%;
    overflow: auto;
    &::-webkit-scrollbar {
      /**/
      width: 0.7rem; /**/
    }
    &::-webkit-scrollbar-thumb {
      /**/
      border-radius: 0.3rem;
      background: rgba(134, 208, 255, 0.3);
    }
    li.tips {
      p {
        margin: 0 auto;
        text-align: center;
        width: 100px;
      }
    }
    li.active {
      margin: 0 1rem 0.6rem 1rem;
      color: rgba(255, 255, 255, 1);
      font-size: 1.4rem;
      height: 11rem;
      min-height: 70px;
      background: rgba(39, 74, 127, 1);
      border: 1px solid rgba(51, 119, 233, 1);
      border-radius: 0.3rem;
      box-sizing: border-box;
      .top-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .alarm-level {
          display: inline-block;
          width: 5rem;
          min-width: 20px;
          min-height: 35px;
          line-height: 5rem;
          font-size: 1.6rem;
          text-align: center;
          margin: 0.5rem;
          border-radius: 0.4rem;
        }
        p {
          margin: 0;
          width: 80%;
          min-width: 150px;
          padding-right: 0.6rem;
          font-size: 1.3rem;
          line-height: 1.6rem;
        }
      }
      .bot-bar {
        text-align: right;
        line-height: 2.6rem;
        margin-top: 0.6rem;
        button {
          font-size: 1.2rem;
          color: #fff;
          background: rgba(54, 114, 195, 1);
          border-radius: 0.3rem;
          margin-right: 1rem;
        }
        :hover {
          background: rgba(61, 209, 255, 1);
          color: rgba(28, 46, 86, 1);
        }
      }
    }
  }
  .set-bar {
    width: 100%;
    padding: 0.6rem 2rem;
    text-align: center;
    font-size: 1.2rem;
    bottom: 1rem;
    button {
      color: #fff;
      width: 10rem;
      background: rgba(54, 114, 195, 1);
      margin-right: 1rem;
      border-radius: 0.3rem;
      &:hover {
        background: rgba(61, 209, 255, 1);
        color: rgba(28, 46, 86, 1);
      }
    }
  }
}
</style>
