import {
  ICanvasState,
  IPos,
  ICanvasToStageConfig,
  ISvgToImageConfig,
  TLayerItem,
  TVectorgraph,
  ILayerCommon
} from "./../types/index.d";

import { TITLE_HEIGHT as titleHeight, bubblePathObj } from "../constVariables";
import { imgUrl } from "@/utils/urls";
import { getWsArr } from "@/utils/auth";

/**
 * @description: 
 * @param {objrct} canvasPos canvas {x,y}
 * @param {object} imgPos  {x,y}
 * @param {number} scale 
 * @return {object} {x,y}
 */
export function getOffset(canvasPos: IPos, imgPos: IPos, scale: number) {
  return {
    x: canvasPos.x - imgPos.x * scale,
    y: canvasPos.y - imgPos.y * scale
  };
}

/**
 * @description:  
 * @param {*} ele html element
 * @param {object} pos  {x,y}
 * @return {object} {left,top}
 */
export function getCorrectPx(
  ele: HTMLElement,
  pos: { left: number; top: number }
) {
  const w = ele.offsetWidth;
  const h = ele.offsetHeight;

  const winWidth = window.innerWidth;
  const winHeight = window.innerHeight;

  const res = { left: pos.left, top: pos.top };
  // 
  if (pos.left + w > winWidth) {
    res.left = winWidth - w;
  }
  // 
  if (pos.top + h + titleHeight > winHeight) {
    res.top = winHeight - titleHeight - h;
  }

  return res;
}

/**
 * @description: konva
 * @param {IPos} pos {x,y}
 * @param {ICanvasState} state {sx,sy,scale}
 * @return {object} {x,y}
 */
export function screenTocanvas(pos: IPos, state: ICanvasState): IPos {
  const { sx, sy, scale } = state;
  return {
    x: (pos.x - sx) / scale,
    y: (pos.y - titleHeight - sy) / scale
  };
}

/**
 * @description: canvasstage
 * @param {ICanvasToStageConfig} data
 * @return {*}
 */
export function canvasToStage(data: ICanvasToStageConfig): IPos {
  const { x, y, sx, sy, scale } = data;
  return {
    x: ~~((x - sx) / scale),
    y: ~~((y - sy - titleHeight) / scale)
  };
}

/**
 * @description:  IPos 
 * @param {string} str
 * @return {*}
 */
export function strToPos(str: string): IPos {
  const [x, y] = str.split(",").map(pos => Number(pos));
  return {
    x,
    y
  };
}

export function svgToImage(config: ISvgToImageConfig, isBubble = false) {
  const { pathSrc, width, height, color } = config;
  const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");

  svg.setAttribute("width", `${width}`);
  svg.setAttribute("height", `${height}`);
  svg.setAttribute("x", "0");
  svg.setAttribute("y", "0");
  svg.setAttribute(
    "viewBox",
    `0 0 ${isBubble ? width : "1024"} ${isBubble ? height : "1024"}`
  );
  const path = document.createElementNS("http://www.w3.org/2000/svg", "path");
  path.setAttribute("d", isBubble ? bubblePathObj.bottom : pathSrc);
  path.setAttribute("width", "100%");
  path.setAttribute("height", "100%");
  path.setAttribute("fill", `${color}`);
  svg.appendChild(path);

  const svgStr = new XMLSerializer().serializeToString(svg);
  const base64 = window.btoa(svgStr);
  const image = new window.Image();
  image.src = `data:image/svg+xml;base64,${base64}`;
  return image;
}

// GUID  xxxxxxxx-xxxx-xxxx-xxxx-xxxxxxxxxxxx
export function guid() {
  function S4() {
    return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
  }
  return (
    S4() +
    S4() +
    "-" +
    S4() +
    "-" +
    S4() +
    "-" +
    S4() +
    "-" +
    S4() +
    S4() +
    S4()
  );
}

//  image DOM
export async function generateImageDomByType(attrs: TLayerItem) {
  const { layrestype } = attrs.defdata;
  if (layrestype === "3") {
    const config = attrs as TVectorgraph;
    const { layinfo, W, H, Color } = config.defdata;
    const svgConfig: ISvgToImageConfig = {
      pathSrc: layinfo,
      width: W,
      height: H,
      color: Color
    };
    const imageDom = await svgToImage(svgConfig);
    return imageDom;
  } else {
    const config = attrs as ILayerCommon;
    const { layinfo } = config.defdata;
    const imageDom = new window.Image();
    imageDom.src = imgUrl + layinfo;
    return imageDom;
  }
}

// ws url 
export const getWsUrl = () => {
  enum WSType {
    location = 3, // 
    alarm = 1, // 
    trans = 199 // 
  }
  const wsUrls = {
    location: "",
    alarm: ""
  };
  const wsArr = getWsArr();
  wsArr.forEach(item => {
    if (item.servertype === WSType.location) {
      wsUrls.location = `ws://${item.serverip}:${item.serverport}/${WSType[3]}`;
    }
    if (item.servertype === WSType.alarm) {
      wsUrls.alarm = `ws://${item.serverip}:${item.serverport}/${WSType[1]}`;
    }
  });
  return wsUrls;
};
