import { IWsItem } from "@/types/login";
import { IDeviceItem } from "@/views/Home/content/types/index";
import Cookies from "js-cookie";

const TokenKey = "hd-water-key";

export function getToken() {
  return Cookies.get(TokenKey);
}

export function setToken(token: string) {
  return Cookies.set(TokenKey, token);
}

export function removeToken() {
  return Cookies.remove(TokenKey);
}

//  eg:
const UserName = "username";

export function setUsername(username: string) {
  return localStorage.setItem(UserName, username);
}

export function getUsername() {
  return localStorage.getItem(UserName) || "";
}

export function removeUsername() {
  return localStorage.removeItem(UserName);
}

//  eg:zscb
const UserAccount = "useraccount";
export function setUserAccount(account: string) {
  return localStorage.setItem(UserAccount, account);
}

export function getUserAccount() {
  return localStorage.getItem(UserAccount) || "";
}

export function removeUserAccount() {
  return localStorage.removeItem(UserAccount);
}

