const wsArr = "serveritem";

export function setWsArr(ws: IWsItem[]) {
  return localStorage.setItem(wsArr, JSON.stringify(ws));
}

export function getWsArr() {
  return (JSON.parse(localStorage.getItem(wsArr) as string) || []) as IWsItem[];
}

export function removeWsArr() {
  return localStorage.removeItem(wsArr);
}

