<template>
  <div>
    <svg-icon
      @click="click"
      :data="isFullscreen ? exitFullscreen : fullscreen"
    />
  </div>
</template>

<script>
import screenfull from "screenfull";
import fullscreen from "@/icons/svg/fullscreen.svg";
import exitFullscreen from "@/icons/svg/exit-fullscreen.svg";

export default {
  name: "Screenfull",
  data() {
    return {
      isFullscreen: false,
      fullscreen,
      exitFullscreen
    };
  },
  mounted() {
    this.init();
  },
  beforeUnmount() {
    this.destroy();
  },
  methods: {
    click() {
      if (!screenfull.isEnabled) {
        this.$message({
          message: "",
          type: "warning"
        });
        return false;
      }
      screenfull.toggle();
    },
    change() {
      this.isFullscreen = screenfull.isFullscreen;
    },
    init() {
      if (screenfull.isEnabled) {
        screenfull.on("change", this.change);
      }
    },
    destroy() {
      if (screenfull.isEnabled) {
        screenfull.off("change", this.change);
      }
    }
  }
};
</script>
