
import { defineComponent, reactive, ref, onMounted } from "vue";
import api from "@/api/basestation";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IBasestaionArrItem } from "../types";
import { getPagination, getMap, getPagintionCount } from "@/utils";
import { getAllDevice } from "@/utils/auth";
import { ElMessage } from "element-plus";
const CLICKED = "clicked";

export default defineComponent({
  name: "Basestation",
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      size: 10,
      prop: "dnum",
      search: ""
    });
    const tableData = ref<IBasestaionArrItem[]>([]);
    const total = ref(0);

    async function getList() {
      // const res: unknown = await api.getStationList(params);
      const deviceAll: any = getAllDevice();
      const stationAll: any = [];
      deviceAll.filter((item: any) => {
        if (item.dtypecode === "LocateStation") {
          stationAll.push(item);
        }
      });
      const res =
        params.search === ""
          ? getPagination(stationAll, params.page)
          : getPagination(stationAll, params.page, params.prop, params.search);
      tableData.value = res;
      // tableData.value.forEach(value => {
      //   value.status =
      //     +new Date() - value.last_heart_time > 3600000 ? "" : "";
      // });
      total.value = getPagintionCount(
        stationAll,
        params.prop,
        params.search
      ) as number;
    }

    function handleEdit(row: IBasestaionArrItem) {
      console.log(row);
    }
    async function getBaseStaionDetail(id: string) {
      const params = {
        act: "fCGtP4nXdRZjr6TYeiN1611196242913",
        DeviceIdS: id
      };

      const res: unknown = await api.getStationData(params);
      const { data } = res as any;
      emit("showEquipDetails", data[0][0], TYPES.basestation);
      emit(CLICKED);
    }
    function handleDetail(row: IBasestaionArrItem) {
      getBaseStaionDetail(row.dids);
    }

    function handleDelete(row: IBasestaionArrItem) {
      api
        .delStation(row.dids)
        .then(() => {
          ElMessage.success("");
        })
        .catch(err => {
          ElMessage.error(err);
        });
    }

    function addEquip() {
      emit("addEquip", TYPES.camera);
    }

    async function changePage(page: number) {
      params.page = page;
      getList();
    }

    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      tableData,
      total,
      handleEdit,
      handleDetail,
      handleDelete,
      changePage,
      search,
      addEquip,
      getMap
    };
  }
});
