import http from "@/utils/http3";

export default {
  // 
  getLabelData(data: any) {
    return http({
      url: `/rcaec/bigwa`,
      method: "post",
      data
    });
  },
  // 
  getLabelList(params: any) {
    return http({
      url: `/rcaec/bigwa`,
      method: "post",
      params
    });
  }
};
