import http from "@/utils/http3";

export default {
  // 
  addStation(data: any) {
    return http({
      url: "/equip/baseStation/",
      method: "post",
      data
    });
  },
  // 
  delStation(id: string | number) {
    return http({
      url: `/equip/baseStation/${id}/`,
      method: "delete"
    });
  },
  // 
  getStationData(data: any) {
    return http({
      url: `/rcaec/bigwa`,
      method: "post",
      data
    });
  },
  // 
  getStationList(data: any) {
    const { page, size } = data;
    return http({
      url: `/equip/baseStation/?page=${page}&size=${size}`
    });
  }
};
