import http from "@/utils/http3";

export default {
  // 
  addCamera(data: any) {
    return http({
      url: "/equip/camera/",
      method: "post",
      data
    });
  },
  // 
  delCamera(id: string | number) {
    return http({
      url: `/equip/camera/${id}/`,
      method: "delete"
    });
  },
  // 
  getCameraData(data: any) {
    return http({
      url: `/rcaec/bigwa`,
      method: "post",
      data
    });
  },
  // 
  getCameraList(params: any) {
    return http({
      url: `/equip/camera`,
      params
    });
  }
};
