import { ICommonRule, ICustomFuncRule } from "@/types";
// Parse the time to string
export const parseTime = (
  time?: object | string | number | null,
  cFormat?: string
): string | null => {
  if (time === undefined || !time) {
    return null;
  }
  const format = cFormat || "{y}-{m}-{d} {h}:{i}:{s}";
  let date: Date;
  if (typeof time === "object") {
    date = time as Date;
  } else {
    if (typeof time === "string") {
      if (/^[0-9]+$/.test(time)) {
        // support "1548221490638"
        time = parseInt(time);
      } else {
        // support safari
        // https://stackoverflow.com/questions/4310953/invalid-date-in-safari
        time = time.replace(new RegExp(/-/gm), "/");
      }
    }
    if (typeof time === "number" && time.toString().length === 10) {
      time = time * 1000;
    }
    date = new Date(time);
  }
  const formatObj: { [key: string]: number } = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  };
  const timeStr = format.replace(/{([ymdhisa])+}/g, (result, key) => {
    const value = formatObj[key];
    // Note: getDay() returns 0 on Sunday
    if (key === "a") {
      return ["", "", "", "", "", "", ""][value];
    }
    return value.toString().padStart(2, "0");
  });
  return timeStr;
};

/**
 * @description: rule
 * @param {string} msg 
 * @return {object} IRule
 */
export function commonRuleItem(msg: string): ICommonRule {
  return {
    required: true,
    message: msg,
    trigger: ["blur", "change"]
  };
}

/**
 * @description: 
 * @param {function} func 
 * @return {IRule} IRule
 */
export function customFuncRule(func: Function): ICustomFuncRule {
  return {
    required: true,
    validator: func,
    trigger: "blur"
  };
}
/**
 * @description: 
 */
export function checkPositiveInteger(
  rule: any,
  value: string,
  callback: Function
) {
  if (value === "0" || value === "") {
    return callback(new Error("0"));
  }
  if (!/(^[1-9]\d*$)/.test(value)) {
    callback(new Error(""));
  } else {
    callback();
  }
}
/**
 * @description: 
 * @param allArr 
 * @param page 
 * @param prop 
 * @param key 
 * @param size 
 * @return arr 
 */
export function getPagination(
  allArr: any[],
  page: number,
  prop = "",
  key = "",
  size = 10
) {
  const len = arguments.length;
  if (len > 2) {
    allArr = allArr.filter((item: any) => item[prop].includes(key)).slice();
  }
  const arr = allArr.slice().slice((page - 1) * size, (page - 1) * size + size);

  return arr;
}
/**
 * @description: 
 * @param allArr 
 * @param prop 
 * @param key 
 * @return count 
 */
export function getPagintionCount(allArr: any, prop = "", key = "") {
  const len = arguments.length;
  if (len > 1) {
    allArr = allArr.filter((item: any) => item[prop].includes(key)).slice();
  }
  const count = allArr.length;
  return count;
}
/**
 * @description 
 * @param snum 
 * @return map 
 */
export function getMap(snum: string) {
  // localStorage
  const sceneAllStr = localStorage.getItem("sceneAll") as string;
  const sceneAll: [] = JSON.parse(sceneAllStr);
  let map = "";
  sceneAll.filter((item: any) => {
    if (item.snum === snum) {
      map = item.sname;
      return;
    }
  });
  return map;
}
/**
 * @description 
 * @param timestr 
 * @return timestr 
 */
export function formatTime(timestr: string) {
  if (!timestr) return;
  timestr = timestr.replace(/T|\+08:00/gim, " ");
  return timestr;
}