
import dragDialog from "../directive/drag-dialog";
import { cameraDirectionFormate } from "../filter";
import { TYPES, cameraDirection } from "../constVariables";
import { commonRuleItem, customFuncRule } from "@/utils";
import {
  computed,
  defineComponent,
  nextTick,
  PropType,
  reactive,
  ref,
  watch
} from "vue";
import { bgImageConfig } from "../constVariables";
import { TShapeType } from "../types";
import { IELForm } from "../types/element";

const UPDATE_X = "updateX";
const UPDATE_Y = "updateY";
const CONFIRM = "confirm";
const CHANGE_DIRECTION = "changeDirection";
const CANCEL_FORM = "cancelForm";
const CLOSE = "close";

export default defineComponent({
  name: "FormContent",
  directives: { "drag-dialog": dragDialog },
  props: {
    // 
    isAdd: {
      type: Boolean,
      default: false
    },
    formType: {
      type: String as PropType<TShapeType>,
      default: TYPES.person
    },
    editable: {
      type: Boolean,
      default: false
    },
    x: { type: Number, default: 0 },
    y: { type: Number, default: 0 },
    formData: {
      type: Object,
      default: () => ({})
    },
    mapId: {
      type: [String, Number],
      required: true
    }
  },
  emits: [UPDATE_X, UPDATE_Y, CONFIRM, CHANGE_DIRECTION, CANCEL_FORM, CLOSE],
  setup(props, { emit }) {
    const formContentRef = ref();

    const form = reactive({
      // 
      roomNum: "1",
      src: "/images/staff_big.png",
      // 
      ip: "10.1.200.16",
      x: "",
      y: "",
      type: "",

      AlarmMappingLayerIdS: "",
      BrandCode: "",
      BrandName: "",
      BuildingName: "",
      BuildingNum: "",
      CreateTime: "",
      DeviceName: "",
      DeviceNum: "",
      DeviceObjProperty: "",
      DeviceSelfPointRelation: "",
      DeviceType: "",
      DeviceTypeCode: "",
      InstallSceneNum: "",
      IsUse: true,
      OwnAlarmSourceCode: "",
      PDeviceIds: "",
      SceneName: "",

      // 
      direction: "1",
      // 
      shapeName: "",
      desc: ""
    });

    const checkIp = (rule: any, value: string, callback: Function) => {
      if (!value) {
        return callback(new Error("ip"));
      }
      const pattern = /((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})(\.((2(5[0-5]|[0-4]\d))|[0-1]?\d{1,2})){3}/g;
      if (pattern.test(value)) {
        callback();
      } else {
        return callback(new Error("ip"));
      }
    };

    const checkPos = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      if (!value) {
        return callback(new Error(""));
      }
      value = Number(value);
      if (!Number.isInteger(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    const checkPosX = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      const w = bgImageConfig.width;
      if (value >= w) {
        callback(new Error(`${w - 1}`));
      } else if (value < 0) {
        callback(new Error(`x0`));
      } else {
        emit(UPDATE_X, value);
        callback();
      }
    };
    const checkPosY = (
      rule: any,
      value: string | number,
      callback: Function
    ) => {
      const h = bgImageConfig.height;
      if (value >= h) {
        callback(new Error(`${h - 1}`));
      } else if (value < 0) {
        callback(new Error(`y0`));
      } else {
        emit(UPDATE_Y, value);
        callback();
      }
    };

    const rules = {
      shapeName: [commonRuleItem("")],
      ip: [customFuncRule(checkIp)],
      x: [customFuncRule(checkPos), customFuncRule(checkPosX)],
      y: [customFuncRule(checkPos), customFuncRule(checkPosY)],
      type: [commonRuleItem("")],
      direction: [commonRuleItem("")],
      DeviceNum: [commonRuleItem("ip")]
    };
    const types = TYPES;

    const formTitle = computed(() => {
      const title = {
        [TYPES.camera]: "",
        [TYPES.basestation]: "",
        [TYPES.labelList]: "",
        [TYPES.person]: ""
      };
      return title[props.formType];
    });

    /**
     * @description: 
     * @param {*} direction
     * @return {*}
     */
    function changeDirection(direction: string) {
      emit(CHANGE_DIRECTION, direction);
    }
    function handleClickHeaderClose() {
      const emitName = props.editable ? CANCEL_FORM : CLOSE;
      emit(emitName);
    }

    function resetFields() {
      (formContentRef.value as IELForm).resetFields();
    }

    function handleClick() {
      (formContentRef.value as IELForm).validate((valid: boolean) => {
        if (valid) {
          //  form
          emit(CONFIRM, form);
        }
      });
    }

    watch(
      () => props.formData,
      newVal => {
        nextTick(() => {
          if (newVal) {
            console.log("newVal", newVal);
            Object.assign(form, newVal);
            form.x = String(props.x);
            form.y = String(props.y);
          }
        });
      }
    );

    watch(
      () => props.x,
      newX => {
        console.log("x", newX);
        if (!isNaN(newX)) {
          form.x = String(newX);
        }
      }
    );
    watch(
      () => props.y,
      newY => {
        if (!isNaN(newY)) {
          form.y = String(newY);
        }
      }
    );

    // ?  y0
    // watch([() => props.x, () => props.y], ([newX], [newY]) => {
    //   console.log(newX);
    //   console.log(newY);

    //   if (!isNaN(newX)) {
    //     form.x = String(newX);
    //   }
    //   if (!isNaN(newY)) {
    //     form.y = String(newY);
    //   }
    // });

    return {
      formContentRef,
      cameraDirectionFormate,
      formTitle,
      form,
      rules,
      cameraDirection,
      changeDirection,
      handleClickHeaderClose,
      types,
      resetFields,
      handleClick
    };
  }
});
