
import dragDialog from "../directive/drag-dialog";
import { imgUrl } from "@/utils/urls";

import { defineComponent, PropType } from "vue";
import { IMapArrItem } from "../types";

const CLOSED = "closed";
const ChangeMap = "changeMap";

export default defineComponent({
  name: "mapChoosen",
  directives: { "drag-dialog": dragDialog },
  props: {
    mapId: {
      type: String,
      required: true
    },
    mapList: {
      type: Object as PropType<IMapArrItem[]>,
      default: () => []
    }
  },
  emits: [CLOSED, ChangeMap],
  setup(props, { emit }) {
    function handleClickHeaderClose() {
      emit(CLOSED);
    }
    function changeMap(mapId: string) {
      if (mapId === props.mapId) return;
      emit(ChangeMap, mapId);
    }
    return {
      imgUrl,
      handleClickHeaderClose,
      changeMap
    };
  }
});
