<template>
  <div>
    <el-form :model="params" label-width="80px" :inline="true" size="small">
      <!-- <el-form-item label="">
        <el-button type="primary" @click="addEquip"></el-button>
      </el-form-item> -->
      <el-form-item label="">
        <el-input v-model="params.search" clearable @change="search" />
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" @click="search"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tableData" border tooltip-effect="dark">
      <el-table-column
        label=""
        prop="dnum"
        show-overflow-tooltip
        align="center"
        width="110"
      />
      <el-table-column
        label=""
        prop="dname"
        show-overflow-tooltip
        align="center"
      />
      <el-table-column label="" show-overflow-tooltip align="center">
        <template #default="{row}">
          {{ getMap(row.snum) }}
        </template>
      </el-table-column>
      <!-- <el-table-column label="" align="center" prop="status" width="90">
        <template #default="{row}">
          <el-tag :type="`${row.status === '' ? 'danger' : 'success'}`">{{
            row.status
          }}</el-tag>
        </template>
      </el-table-column> -->
      <el-table-column width="160" label="" align="center">
        <template #default="{row}">
          <el-button size="mini" type="primary" @click="handleDetail(row)"
            ></el-button
          >
          <!-- <el-button size="mini" @click="handleEdit(row)">Edit</el-button> -->
          <!-- <el-popconfirm
            :title="` ${row.ip} `"
            @confirm="handleDelete(row)"
          >
            <template #reference>
              <el-button type="danger" size="mini" style="margin-left:10px;"
                ></el-button
              >
            </template>
          </el-popconfirm> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      v-show="total && tableData && tableData.length"
      background
      v-model:current-page="params.page"
      :page-size="params.size"
      layout="prev, pager, next"
      :total="total"
      @current-change="changePage"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref, onMounted } from "vue";
import api from "@/api/basestation";
import { TYPES } from "../constVariables";
import { ICommonTableParams, IBasestaionArrItem } from "../types";
import { getPagination, getMap, getPagintionCount } from "@/utils";
import { getAllDevice } from "@/utils/auth";
import { ElMessage } from "element-plus";
const CLICKED = "clicked";

export default defineComponent({
  name: "Basestation",
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      size: 10,
      prop: "dnum",
      search: ""
    });
    const tableData = ref<IBasestaionArrItem[]>([]);
    const total = ref(0);

    async function getList() {
      // const res: unknown = await api.getStationList(params);
      const deviceAll: any = getAllDevice();
      const stationAll: any = [];
      deviceAll.filter((item: any) => {
        if (item.dtypecode === "LocateStation") {
          stationAll.push(item);
        }
      });
      const res =
        params.search === ""
          ? getPagination(stationAll, params.page)
          : getPagination(stationAll, params.page, params.prop, params.search);
      tableData.value = res;
      // tableData.value.forEach(value => {
      //   value.status =
      //     +new Date() - value.last_heart_time > 3600000 ? "" : "";
      // });
      total.value = getPagintionCount(
        stationAll,
        params.prop,
        params.search
      ) as number;
    }

    function handleEdit(row: IBasestaionArrItem) {
      console.log(row);
    }
    async function getBaseStaionDetail(id: string) {
      const params = {
        act: "fCGtP4nXdRZjr6TYeiN1611196242913",
        DeviceIdS: id
      };

      const res: unknown = await api.getStationData(params);
      const { data } = res as any;
      emit("showEquipDetails", data[0][0], TYPES.basestation);
      emit(CLICKED);
    }
    function handleDetail(row: IBasestaionArrItem) {
      getBaseStaionDetail(row.dids);
    }

    function handleDelete(row: IBasestaionArrItem) {
      api
        .delStation(row.dids)
        .then(() => {
          ElMessage.success("");
        })
        .catch(err => {
          ElMessage.error(err);
        });
    }

    function addEquip() {
      emit("addEquip", TYPES.camera);
    }

    async function changePage(page: number) {
      params.page = page;
      getList();
    }

    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      tableData,
      total,
      handleEdit,
      handleDetail,
      handleDelete,
      changePage,
      search,
      addEquip,
      getMap
    };
  }
});
</script>
