
import { onMounted, reactive, ref, defineComponent } from "vue";
import api from "@/api/label";
import { TYPES } from "../constVariables";
import { ICommonTableParams, ILabelItem } from "../types";
import { getPagination, getPagintionCount, getMap } from "@/utils";
import { getAllDevice } from "@/utils/auth";
const CLICKED = "clicked";

export default defineComponent({
  name: "Label",
  setup(props, { emit }) {
    const params = reactive<ICommonTableParams>({
      page: 1,
      size: 10,
      prop: "dnum",
      search: ""
    });

    const tableData = ref<ILabelItem[]>([]);
    const total = ref(0);

    async function getList() {
      // const res: unknown = await api.getLabelList(params);
      const deviceAll: any = getAllDevice();
      const labelAll: any = [];
      deviceAll.filter((item: any) => {
        if (item.dtypecode === "Epaulet") {
          labelAll.push(item);
        }
      });
      const res =
        params.search === ""
          ? getPagination(labelAll, params.page)
          : getPagination(labelAll, params.page, params.prop, params.search);
      tableData.value = res;
      console.log("label", res);
      total.value = getPagintionCount(
        labelAll,
        params.prop,
        params.search
      ) as number;
    }

    async function handleEdit(row: ILabelItem) {
      console.log(row);
    }
    async function getPersonDetail(id: string) {
      const params = {
        act: "fCGtP4nXdRZjr6TYeiN1611196242913",
        DeviceIdS: id
      };

      const res: unknown = await api.getLabelData(params);
      const { data } = res as any;
      emit("showEquipDetails", data[0][0], TYPES.labelList);
      emit(CLICKED);
    }
    function handleDetail(row: ILabelItem) {
      getPersonDetail(row.dids);
    }
    async function handleDelete(row: ILabelItem) {
      console.log(row);
    }

    async function changePage(page: number) {
      params.page = page;
      getList();
    }
    async function search() {
      params.page = 1;
      getList();
    }

    onMounted(() => {
      getList();
    });

    return {
      params,
      tableData,
      total,
      handleEdit,
      handleDelete,
      changePage,
      search,
      getMap,
      handleDetail
    };
  }
});
